<template>
  <div id="app">
    <router-view v-if="isRouterAlive" />

  </div>
</template>

<script>
export default {
  name: "app",
  provide() {
    return {
      reload: this.reload
    };
  },
  data() {
    return {
      isRouterAlive: true
    };
  },
  methods: {
    reload() {
      this.isRouterAlive = false;
      this.$nextTick(function() {
        this.isRouterAlive = true;
      });
    }
  }
};
</script>


<style lang='less'>

// 
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;

  text-decoration: none;
}

html {
  height: 100%;
}

body {
  height: 100%;
}

#app {
  height: 100%;
}

// .wrapper {
//   height: 100%;
// }
//
.wrapper-inner {
  height: 100%;

  background: #fff;
}

//
.side-container {
  font-size: 14px;

  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;

  width: 200px;
}

//
.main-container {
  height: 100%;
  margin-left: 200px;
}

/* Table */
.table-container .el-table th {
  text-align: center;
}

/**/
.login-container {
  .el-input-group__prepend {
    color: #141414;
    background: #eee;
  }
}

//
.underOne {
  height: 1200px;
  padding: 5px 5px 20px 5px;

  border-top-left-radius: 50px;
  border-top-right-radius: 50px;
  background-color: rgb(224, 226, 226);
}

//
.page {
  height: 1200px;
  padding: 5px;

  border-top-left-radius: 50px;
  border-top-right-radius: 50px;
  background-color: #dfd9d9;
}

//
.block {
  height: 5px;
  margin: 5px 0;
}

//
.el-form-item__label {
  width: 90px !important;
}

//
.el-form-item__content {
  margin-left: 110px !important;
}

//
.di {
  text-align: center;
}

//
.cen-box {
  margin-bottom: 40px;
}

//
.dl-center {
  float: left;

  width: 650px;
}

//
.dr-center {
  float: right;

  margin-right: 10px;
}

//
.dark {
  display: none!important;
}

//
.layout-menu {
  display: none;
}

//
.container-width {
  margin-left: 0;
}

.xiangqingtitle {
  font-family: 'Helvetica Neue',Helvetica,
  'PingFang SC','Hiragino Sans GB',
  'Microsoft YaHei','',Arial,sans-serif;

  text-align: center;

  color: darkgray;
}

.tablecon {
  height: 100%;
}

// 
.lostitle {
  margin-bottom: 20px;
  padding-bottom: 10px;

  border-bottom: 1px solid rgba(90, 138, 201, .432);
}

.content {
  padding: 20px;

  background-color: #f4f8ff;
}

//
.gotop {
  position: fixed;
  z-index: 9999;
  right: 13px;
  bottom: 13px;

  display: none;
  width: 60px;
  height: 60px;
  text-align: center;
  line-height: 60px;
  cursor: pointer;
  -webkit-transition: .2s;
          transition: .2s;
  -webkit-transition-timing-function: linear; /* Safari and Chrome */
          transition-timing-function: linear;
  background: #ccc;
  opacity: .4;
  border: 1px solid blue;
  border-radius: 50%;
}

.gotop:hover {
  bottom: 18px;
  opacity: 1;
  border: 2px solid rgb(115, 255, 0);
}










































</style>



