<template>

<!-- truefalse -->
  <div v-if="userName==this.$store.state.nameler?false:true">
    

    <div class="topBanner">
      
      <el-tooltip class="item" effect="light" content="" placement="bottom">
      <p class='tog' @click="togglemenu">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-zhankai1"></use>
        </svg></p>
      </el-tooltip>

      <p class='left'></p>

      <p class='right' @click="goBack" >
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-shouye"></use>
        </svg></p>

      <p class='right' title="">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-character"></use>
        </svg>{{userName}}</p>
      
      <p class='right' @click="screenfull">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-quanping4"></use>
      </svg></p>

      <p class='right' title="">
      <router-link to="/yonghuguanli/mimaxiugai" class="roulink">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-mimazhongzhi"></use>
      </svg>
      </router-link>
      </p>

      <p class='right' @click="lastpage">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-fanhui2"></use>
      </svg></p>
    </div>

  <div class="content">
  <el-row>
  <el-col :span="24">
    <div class="changepw">
        <el-form :model="ruleForm2" status-icon :rules="rules2" ref="ruleForm2" label-width="100px" class="demo-ruleForm">
            <el-form-item label="" prop="oldPassword">
                <el-input type="password" show-password v-model="ruleForm2.oldPassword" autocomplete="off" clearable></el-input>
            </el-form-item>
            
            <el-form-item label="" prop="password">
                <el-input type="password" show-password v-model="ruleForm2.password" autocomplete="off" clearable></el-input>
            </el-form-item>

            <el-form-item label="" prop="checkPass">
                <el-input type="password" show-password v-model="ruleForm2.checkPass" autocomplete="off" clearable></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" plain @click="submitForm('ruleForm2')" size="small"></el-button>
                <el-button type="primary" plain @click="resetForm('ruleForm2')" size="small"></el-button>
            </el-form-item>
        </el-form>
    </div>
</el-col>
</el-row>
  </div>
  </div>
</template>
<script>
// 
import {formatDate} from '../../common/date.js';
import screenfull from 'screenfull'
//vuex
// import {mapState} from 'vuex'

export default {
  data() {
    //
        var validatePass = (rule, value, callback) => {
        if (value  ==='') {
          callback(new Error('!'));
        } else {
          callback();
        }
      };

    //
      var validatePass1 = (rule, value, callback) => {
        if (value == this.ruleForm2.oldPassword) {
          callback(new Error('!'));
        } else if (value ==='' ) {
          callback(new Error('!'));
        }else if (this.ruleForm2.oldPassword ==='' ) {
          callback(new Error('!'));
        }  else {
          callback();
        }
      };

    //
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else if (value !== this.ruleForm2.password) {
          callback(new Error('!'));
        } else {
          callback();
        }
      };
      
    return {
        //
        rules2: {
        //
          oldPassword: [
            { validator: validatePass, trigger: 'blur' }
          ],
        //
          password: [
            { validator: validatePass1, trigger: 'blur' }
          ],
         //
          checkPass: [
            { validator: validatePass2, trigger: 'blur' }
          ],
        },
      isFullscreen: false,
      userName: "",

       ruleForm2: {
          uid:0,
          oldPassword:'',
          password:'',
          checkPass:''
        },
        
    };
  },
  // mounted,html
  mounted() {
    this.getName();//

    window.onresize = () => {
            // ESC
            if (!this.checkFull()) {
              // esc
              this.isFullscreen = false
            }
          };

    // this.getlist();//
  },

  methods: {
    //
    lastpage(){
      window.history.go(-1);
    },
    //   
    submitForm(formName) {
        
        let uid = sessionStorage.getItem("userId");
        this.ruleForm2.uid = parseInt(uid);//
        this.$http
          .post(
            this.$store.state.domain +"/qdzj/user/updatePassword",this.ruleForm2,{
                //
                emulateJSON: true
            }
          )
          //then()then()then()
          .then(  
            // Lambda
            response => {
              if (response.data.code == 0) {
                  this.$alert(response.data.detail.result, '', {
                    confirmButtonText: '',
                    type: 'success',
                    callback: action => {
                      //home
                       this.$router.push("../home");
                    }
                    });
              } else {
              //
                this.listLoading=false;
                this.$message({
                    message: response.body.msg,
                    type: "error"
                  });
              }
              
            },
            response => {
              //
                this.$message({
                    dangerouslyUseHTMLString: true,//html
                    message: '<svg class="icon" aria-hidden="true"> <use xlink:href="#icon-shengqi"></use> </svg> '+
                    response.body.msg,
                    type: "error"
                  });
              console.log(response);
            }
          );
    },
    // 
    resetForm(formName) {
        this.$refs[formName].resetFields();
    },

    /**
     * 
     */
    screenfull() {
      if (!screenfull.enabled) {
        this.$message({
          message: 'Your browser does not work',
          type: 'warning'
        })
        return false
      }
      screenfull.toggle()
      this.isFullscreen = true
    },
    /**
     * ESC
     */
    checkFull() {
      var isFull = document.fullscreenEnabled || window.fullScreen || document.webkitIsFullScreen || document.msFullscreenEnabled
      // to fix : false || undefined == undefined
      if (isFull === undefined) {
          isFull = false
      }
      return isFull
    },


    //
    togglemenu(){
        this.$store.state.isLeftHiden=!this.$store.state.isLeftHiden;
        console.log(this.$store.state.isLeftHiden)
    },
    
    // 
      goBack() {
        var _this = this;
				this.$confirm('?', '', {
					//type: 'warning'
				}).then(() => {
          window.sessionStorage.removeItem('name');//sessionname
					this.$router.push('/login');
				}).catch(() => {
			});
    },
    //vue  sessionStorage
      getName() {
        let userName = sessionStorage.getItem("name");
        this.userName = userName;
      },


  },
    //
    filters: {
          formatDate(time) {
              var date = new Date(time);
              return formatDate(date, 'yyyy-MM-dd hh:mm');
          }
    },
     
};
</script>

 <style lang='less'>

//  less
@import '../users/index.css';

.changepw {
  padding: 50px 200px 50px 500px;

  background-color: rgba(238, 238, 238, .835);
}




































</style>



