<template>

<!-- truefalse -->
  <div v-if="userName==this.$store.state.nameler?false:true">
    

    <div class="topBanner">
      
      <el-tooltip class="item" effect="light" content="" placement="bottom">
      <p class='tog' @click="togglemenu">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-zhankai1"></use>
        </svg></p>
      </el-tooltip>

      <p class='left'> >> </p>

      <p class='right' @click="goBack" >
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-shouye"></use>
        </svg></p>

      <p class='right' title="">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-character"></use>
        </svg>{{userName}}</p>
      
      <p class='right' @click="screenfull">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-quanping4"></use>
      </svg></p>

      <p class='right' title="">
      <router-link to="/yonghuguanli/mimaxiugai" class="roulink">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-mimazhongzhi"></use>
      </svg>
      </router-link>
      </p>

      <p class='right' @click="lastpage">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-fanhui2"></use>
      </svg></p>
    </div>
  
  <div class="content">
        <div class="searcharea">
          <!-- el-formmodelel-form-itemel-form-itemprop -->
          <el-form ref="searchform" :model="searchform" label-width="80px" size="mini">

            <el-row :gutter="30">
              <el-col :span="6">
                <el-form-item label="" prop="date">
                    <el-date-picker class="shi"
                      v-model="searchform.date"
                      clearable
                      value-format="yyyyMMdd"
                      type="date"
                      placeholder="">
                    </el-date-picker>
                </el-form-item>
              </el-col>

              <el-col :span="6">
                <div class="di">
                  <div class="wrapper">
                    <el-button type="primary" plain @click="search()" size="mini">
                      </el-button>
                    <el-button type="primary" plain @click="resetForm('searchform')" size="mini">
                      
                    </el-button>
                  </div>
                </div>
              </el-col>
            </el-row>
          </el-form>
        </div>

        <!-- ===============================  =================================== -->
       
          <el-table :data="tableData"  border size="medium"
          stripe v-loading="listLoading" 
          element-loading-text=""
          element-loading-spinner="el-icon-loading"
          element-loading-background="rgba(0, 0, 0, 0.8)"
          style="width: 100%; height:100%;">
            <el-table-column prop="reportName" label="" align="center">
            </el-table-column>
            <el-table-column prop="reportDate" label="" align="center">
            </el-table-column>
            <el-table-column prop="reportType" label="" align="center" v-if="false">
            </el-table-column>
            <el-table-column prop="" label=""  align="center">
              <template slot-scope="scope">
                <el-button @click="handleClick(scope.row.reportName,scope.row.reportDate,scope.row.reportType)" type="text" size="small"></el-button>
              </template>
            </el-table-column>
          </el-table>
  </div>
  </div>
</template>
<script>
// 
import {formatDate} from '../../common/date.js';
import screenfull from 'screenfull'
//vuex
// import {mapState} from 'vuex'

export default {
  data() {
    return {
      isFullscreen: false,
      userName: "",
      listLoading: false,//

      //
      tableData: [],

      searchform: {
        date: "",//
      }
    };
  },
  // mounted,html
  mounted() {
    this.getName();//
    window.onresize = () => {
            // ESC
            if (!this.checkFull()) {
              // esc
              this.isFullscreen = false
            }
          }
  },

  //html
created() {
    // this.getlist();//
},
  methods: {
    //
    handleClick(reportName,reportDate,reportType){
          let data={
            date:reportDate,
            financeType:reportType
          }
          const url = this.$store.state.domain +"/financeFile/download";
                this.$http.post(url, data , {
                    responseType: 'blob',
                    emulateJSON:true
                }).then(res => {
                let blob = new Blob([res.data], {
                    type: 'application/vnd.ms-excel,application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'
                })
                if (window.navigator.msSaveOrOpenBlob) {
                    navigator.msSaveBlob(blob);
                } else {
                    let elink = document.createElement('a');
                    elink.download = reportDate+"_"+reportName;
                    console.log(this.getdate())
                    elink.style.display = 'none';
                    elink.href = URL.createObjectURL(blob);
                    document.body.appendChild(elink);
                    elink.click();
                    document.body.removeChild(elink);
                }
            }).catch(err => {
                console.warn(err);
            });     
    },

    //
    getdate() {
          var now = new Date(),
              y = now.getFullYear(),
              m = now.getMonth() + 1,
              d = now.getDate();
          return y + "-" + (m < 10 ? "0" + m : m) + "-" + (d < 10 ? "0" + d : d);
      },
      
    //
    lastpage(){
      window.history.go(-1);
    },
    /**
     * 
     */
    screenfull() {
      if (!screenfull.enabled) {
        this.$message({
          message: 'Your browser does not work',
          type: 'warning'
        })
        return false
      }
      screenfull.toggle()
      this.isFullscreen = true
    },
    /**
     * ESC
     */
    checkFull() {
      var isFull = document.fullscreenEnabled || window.fullScreen || document.webkitIsFullScreen || document.msFullscreenEnabled
      // to fix : false || undefined == undefined
      if (isFull === undefined) {
          isFull = false
      }
      return isFull
    },


    //
    togglemenu(){
        this.$store.state.isLeftHiden=!this.$store.state.isLeftHiden;
        console.log(this.$store.state.isLeftHiden)
    },

    
      //vue  sessionStorage
      getName() {
        let userName = sessionStorage.getItem("name");
        this.userName = userName;
      },
      
      // 
      goBack() {
        var _this = this;
				this.$confirm('?', '', {
					//type: 'warning'
				}).then(() => {
          window.sessionStorage.removeItem('name');//sessionname
					this.$router.push('/login');
				}).catch(() => {
			});
    },
    
      // 
      search() {
        this.getlist();
      },

      // 
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },

      // ajaxVue  this.$http  HTTP 
      getlist() {
        this.listLoading=true
        this.$http
          .post(
            this.$store.state.domain +"/financeFile/financeList",this.searchform 
          )
          //then()then()then()
          .then(  
            // Lambda
            response => {
              if (response.body.code == 0) {
                //
                this.tableData = response.body.detail;
                this.listLoading=false;

                if(this.tableData=='')
                {
                  this.$notify({
                    message: '',
                    type: 'warning',
                    duration:"2000"//
                  });
                }
              } else {
              //
                this.listLoading=false;
                this.$message({
                    message: response.body.msg,
                    type: "error"
                  });
              }
              
            },
            response => {
              //
                this.listLoading=false;
                this.$message({
                    dangerouslyUseHTMLString: true,//html
                    message: '<svg class="icon" aria-hidden="true"> <use xlink:href="#icon-shengqi"></use> </svg> '+
                    response.body.message,
                    type: "error"
                  });
              console.log(response);
            }
          );
      },
  },

    //
    filters: {
          formatDate(time) {
              var date = new Date(time);
              return formatDate(date, 'yyyy-MM-dd hh:mm');
          }
    },
     
};
</script>

 <style lang='less'>

//  less
@import '../users/index.css';











































































































































</style>



