<template>

<!-- truefalse -->
  <div v-if="userName==this.$store.state.nameler?false:true">
    

    <div class="topBanner">
      
      <el-tooltip class="item" effect="light" content="" placement="bottom">
      <p class='tog' @click="togglemenu">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-zhankai1"></use>
        </svg></p>
      </el-tooltip>

      <p class='left'> >> </p>

      <p class='right' @click="goBack" >
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-shouye"></use>
        </svg></p>

      <p class='right' title="">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-character"></use>
        </svg>{{userName}}</p>
      
      <p class='right' @click="screenfull">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-quanping4"></use>
      </svg></p>

      <p class='right' title="">
      <router-link to="/yonghuguanli/mimaxiugai" class="roulink">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-mimazhongzhi"></use>
      </svg>
      </router-link>
      </p>

      <p class='right' @click="lastpage">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-fanhui2"></use>
      </svg></p>
    </div>
  
<div class="content">
        <div class="searcharea" style="margin-top:20px;">
            <div class="lostitle">
              
            </div>
            
          <!-- el-formmodelel-form-itemel-form-itemprop -->
          <el-form ref="searchform" :model="searchform" label-width="80px" size="mini">

            <el-row :gutter="30">
              <el-col :span="8">
                  <el-form-item label="" prop="channelCd">
                    <el-select v-model="searchform.channelCd" class="sh" multiple collapse-tags placeholder="" @change="changeSelectDay">
                    <el-option v-for="item in channellist"
                        :key="item.channelCode"
                        :label="item.channelCode"
                        :value="item.channelCode"></el-option>
                  </el-select>
                  </el-form-item>
              </el-col>
              
              <el-col :span="8">
                <el-form-item label="" prop="beginDate">
                    <el-date-picker class="shi"
                      v-model="searchform.beginDate"
                      value-format="yyyy-MM-dd"
                      type="date"
                      placeholder="">
                    </el-date-picker>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label="" prop="endDate">
                  <el-date-picker class="shi"
                      v-model="searchform.endDate"
                      value-format="yyyy-MM-dd"
                      type="date"
                      placeholder="">
                    </el-date-picker>
                </el-form-item>
              </el-col>
            </el-row>

            <el-row :gutter="30">
              <el-col :span="24">
                <div class="di">
                  <div class="wrapper">
                    <el-button type="primary" plain @click="outputdaylist('')" size="mini">
                      </el-button>
                    <!-- <el-button type="primary" plain @click="resetForm('searchform')" size="mini">
                      
                    </el-button> -->
                    <el-button type="primary" plain @click="resetForm('searchform')" size="mini">
                      
                    </el-button>
                  </div>
                </div>
              </el-col>
            </el-row>
          </el-form>
        </div>

        <div class="searcharea" style="margin-top:20px;">
            <div class="lostitle">
              
            </div>
            
          <!-- el-formmodelel-form-itemel-form-itemprop -->
          <el-form ref="dbform" :model="dbform" label-width="80px" size="mini">

            <el-row :gutter="30">
              <el-col :span="8">
                  <el-form-item label="" prop="channelCd">
                    <el-select v-model="dbform.channelCd" class="sh"  @change="changeSelectMonth" multiple collapse-tags placeholder="">
                    <el-option v-for="item in channellist"
                        :key="item.channelCode"
                        :label="item.channelCode"
                        :value="item.channelCode"></el-option>
                  </el-select>
                  </el-form-item>
              </el-col>
              
              <el-col :span="8">
                <el-form-item label="" prop="beginDate">
                    <el-date-picker class="shi"
                      v-model="dbform.beginDate"
                      value-format="yyyy-MM-01"
                      type="date"
                      placeholder="">
                    </el-date-picker>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label="" prop="endDate">
                  <el-date-picker class="shi"
                      v-model="dbform.endDate"
                      value-format="yyyy-MM-01"
                      type="date"
                      placeholder="">
                    </el-date-picker>
                </el-form-item>
              </el-col>
            </el-row>

            <el-row :gutter="30">
              <el-col :span="24">
                <div class="di">
                  <div class="wrapper">
                    <el-button type="primary" plain @click="outputmonthlist('')" size="mini">
                      </el-button>
                    <el-button type="primary" plain @click="resetForm('dbform')" size="mini">
                      
                    </el-button>
                  </div>
                </div>
              </el-col>
            </el-row>
          </el-form>
        </div>
</div>

  </div>
</template>
<script>
// 
import {formatDate} from '../../common/date.js';
import screenfull from 'screenfull'
//vuex
// import {mapState} from 'vuex'

export default {
  data() {
    return {
      isFullscreen: false,
      oldOption: [],//
      oldOptions: [],//
      userName: "",

      //
      channellist:[],

      //
      searchform: {
        channelCd: "",//
        beginDate: "",//
        endDate: "",//
      },

      //
      dbform:{
        channelCd: "",//
        beginDate: "",//
        endDate: "",//
      }
    };
  },
  // mounted,html
  mounted() {
    this.getName();//
    
    this.getchannellist();//

    window.onresize = () => {
            // ESC
            if (!this.checkFull()) {
              // esc
              this.isFullscreen = false
            }
          }

    // this.getlist();//
    
  },

  methods: {
    //
    changeSelectDay(val){
      let allValues = []
        //
        for (let item of this.channellist) {
            allValues.push(item.channelCode)
        }

        // 
        const oldVal = this.oldOption.length === 0 ? [] : this.oldOption[1]

        // 
        if (val.includes('')) this.searchform.channelCd = allValues 

        //     
        if (oldVal.includes('') && !val.includes('')) this.searchform.channelCd = []

        //      
        //  
        if (oldVal.includes('') && val.includes('')) {
            const index = val.indexOf('')
            val.splice(index, 1) // 
            this.searchform.channelCd = val
        }

        //    
        if (!oldVal.includes('') && !val.includes('')) {
            if (val.length === allValues.length - 1) this.searchform.channelCd = [''].concat(val)
        }

        //  
        this.oldOption[1] = this.searchform.channelCd
    },

    //
    changeSelectMonth(val) {
        let allValues = []
        //
        for (let item of this.channellist) {
            allValues.push(item.channelCode)
        }

        // 
        const oldVal = this.oldOptions.length === 0 ? [] : this.oldOptions[1]

        // 
        if (val.includes('')) this.dbform.channelCd = allValues 

        //     
        if (oldVal.includes('') && !val.includes('')) this.dbform.channelCd = []

        //      
        //  
        if (oldVal.includes('') && val.includes('')) {
            const index = val.indexOf('')
            val.splice(index, 1) // 
            this.dbform.channelCd = val
        }

        //    
        if (!oldVal.includes('') && !val.includes('')) {
            if (val.length === allValues.length - 1) this.dbform.channelCd = [''].concat(val)
        }

        //  
        this.oldOptions[1] = this.dbform.channelCd
      },


    //
    outputmonthlist(filename){
      //
      if(this.dbform.channelCd=='' || this.dbform.channelCd==null){
        this.$message({
                      message: '',
                      type: "warning"
                    });
      }else{

      //
      const index = this.dbform.channelCd.indexOf('')

      //
      if(index!=-1){
        this.dbform.channelCd.splice(index, 1) // 
      }

      const url = this.$store.state.domain +"/loanManage/reportFormMonthExport";
                this.$http.post(url, this.dbform , {
                    responseType: 'blob'
                }).then(res => {
                  // 
                    this.$message({
                      message: '',
                      type: "success"
                    });
                    let blob = new Blob([res.data], {
                        type: 'application/vnd.ms-excel,application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'
                    })
                    if (window.navigator.msSaveOrOpenBlob) {
                        navigator.msSaveBlob(blob);
                    } else {
                        let elink = document.createElement('a');
                        elink.download = filename+'_'+this.getdate()+".xls";
                        // console.log(this.getdate())
                        elink.style.display = 'none';
                        elink.href = URL.createObjectURL(blob);
                        document.body.appendChild(elink);
                        elink.click();
                        document.body.removeChild(elink);
                    }

                }).catch(err => {
                  // 
                    this.$message({
                      message: err.body.message,
                      type: "error"
                    });
                    // console.warn(err);
                });
      }
    },


    // 
    outputdaylist(filename){
      //
      if(this.searchform.channelCd=='' || this.searchform.channelCd==null){
        this.$message({
                      message: '',
                      type: "warning"
                    });
      }else{
        
      //
      const index = (this.searchform.channelCd).indexOf('')

      //
      if(index!=-1){
        this.searchform.channelCd.splice(index, 1) // 
      }

      const url = this.$store.state.domain +"/loanManage/reportFormDayExport";

      this.$http.post(url, this.searchform , 
      { responseType: 'blob'
                }).then(res => {
                  // 
                    this.$message({
                      message: '',
                      type: "success"
                    });
                    let blob = new Blob([res.data], {
                        type: 'application/vnd.ms-excel,application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'
                    })

                    if (window.navigator.msSaveOrOpenBlob) {
                        navigator.msSaveBlob(blob);
                    } else {
                        let elink = document.createElement('a');

                        elink.download = filename+'_'+this.getdate()+".xls";
                        // console.log(this.getdate())
                        elink.style.display = 'none';
                        elink.href = URL.createObjectURL(blob);
                        document.body.appendChild(elink);
                        elink.click();
                        document.body.removeChild(elink);
                    }

                }).catch(err => {
                  // 
                    this.$message({
                      message: err.body.message,
                      type: "error"
                    });
                    // console.warn(err);
                });
                
      }
    },

    //
    getdate() {
          var now = new Date(),
              y = now.getFullYear(),
              m = now.getMonth() + 1,
              d = now.getDate();
          return y + "-" + (m < 10 ? "0" + m : m) + "-" + (d < 10 ? "0" + d : d);
      },

    //
    lastpage(){
      window.history.go(-1);
    },
    /**
     * 
     */
    screenfull() {
      if (!screenfull.enabled) {
        this.$message({
          message: 'Your browser does not work',
          type: 'warning'
        })
        return false
      }
      screenfull.toggle()
      this.isFullscreen = true
    },
    /**
     * ESC
     */
    checkFull() {
      var isFull = document.fullscreenEnabled || window.fullScreen || document.webkitIsFullScreen || document.msFullscreenEnabled
      // to fix : false || undefined == undefined
      if (isFull === undefined) {
          isFull = false
      }
      return isFull
    },

    //vue  sessionStorage
      getName() {
        let userName = sessionStorage.getItem("name");
        this.userName = userName;
      },
      
     // 
      goBack() {
        var _this = this;
				this.$confirm('?', '', {
					//type: 'warning'
				}).then(() => {
          window.sessionStorage.removeItem('name');//sessionname
					this.$router.push('/login');
				}).catch(() => {
			});
    },
    
    //
    togglemenu(){
        this.$store.state.isLeftHiden=!this.$store.state.isLeftHiden;
        console.log(this.$store.state.isLeftHiden)
    },

    // 
      getchannellist() {
      let data = {
        channelCd: this.searchform.channelCd
      };
      this.$http
        .post(this.$store.state.domain +"/loanManage/channel/query", data)
          .then(
          //
          response => {
            if (response.data.code == 0) { 
              //
                this.channellist.push({channelCode: ''});

                for(var i=0;i<(response.data.detail.result).length;i++){

                  this.channellist.push((response.data.detail.result)[i]);
                }
            } 
            //
            else {
              this.$message({
                message: response.data.msg,
                type: "error"
              });
            }
          },
          //
          response => {
            // console.log(response);
          }
        );
      },

      // 
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },

  },

    //
    filters: {
          formatDate(time) {
              var date = new Date(time);
              return formatDate(date, 'yyyy-MM-dd hh:mm');
          }
    },
     
};
</script>

 <style lang='less'>

//  less
@import '../users/index.css';

</style>



