<template>
  <div v-if="userName=='config'?false:true">
    <div class="topBanner">

      <el-tooltip class="item" effect="dark" content="" placement="bottom">
      <p class='tog' @click="togglemenu">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-zhankai1"></use>
        </svg></p>
      </el-tooltip>

      <p class='left'></p>
      
      <el-tooltip class="item" effect="dark" content="" placement="bottom">
      <p class='right' @click="goBack">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-shouye"></use>
        </svg></p>
      </el-tooltip>

      <el-tooltip class="item" effect="dark" content="" placement="bottom">
      <p class='right'>
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-character"></use>
        </svg>{{userName}}</p>
      </el-tooltip>

    </div>

    <div class="page">
      <div class="underOne">
        <div class="searcharea">
          <el-form ref="searchform" :model="searchform" label-width="80px" size="mini">
            <el-row :gutter="30">

              <el-col :span="6">
                <div class="grid-content bg-purple">
                  <el-form-item label="" prop="name">
                    <el-input v-model="searchform.name"></el-input>
                  </el-form-item>
                </div>
              </el-col>

              <el-col :span="6">
                <div class="grid-content bg-purple">
                  <el-form-item label="" prop="name">
                    <el-input v-model="searchform.phone"></el-input>
                  </el-form-item>
                </div>
              </el-col>

              <el-col :span="6">
                <div class="grid-content bg-purple">
                  <el-form-item label="" prop="name">
                    <el-input v-model="searchform.idNO"></el-input>
                  </el-form-item>
                </div>
              </el-col>

              <el-col :span="6">
                <div class="grid-content bg-purple">
                  <el-form-item label="" prop="channelCd">
                  <el-select v-model="searchform.channelCd" placeholder=""
                  @change="selChange($event)">
                      <!--  -->
                    <el-option v-for="item in channellist"
                              :label="item.channelName"
                              :key="item.channelCode"
                              :value="item.channelCode"
                          ></el-option>
                  </el-select>
                  </el-form-item>
                </div>
              </el-col>

              <el-col :span="6">
                <div class="grid-content bg-purple">
                  <el-form-item label="" prop="productCd">
                  <el-select v-model="searchform.productCd" placeholder="">
                    <!--  -->
                    <el-option v-for="item in productlist"
                              :label="item.productName"
                              :key="item.productCode"
                              :value="item.productCode"
                          ></el-option>
                  </el-select>
                  </el-form-item>
                </div>
              </el-col>

              <el-col :span="6">
                <el-form-item label="" prop="beginDate">
                    <el-date-picker
                      v-model="searchform.beginDate"
                      value-format="yyyy-MM-dd HH:mm:ss"
                      type="datetime"
                      placeholder=""
                      default-time="00:00:00">
                    </el-date-picker>
                </el-form-item>
              </el-col>

              <el-col :span="6">
                <el-form-item label="" prop="endDate">
                  <el-date-picker
                      v-model="searchform.endDate"
                      value-format="yyyy-MM-dd HH:mm:ss"
                      type="datetime"
                      placeholder=""
                      default-time="23:59:59">
                    </el-date-picker>
                </el-form-item>
              </el-col>

              <el-col :span="6">
                <div class="di">
                  <span class="wrapper">
                    <!--  -->
                    <el-button :type="status=='none'?'primary':''" @click="search()" size="mini">
                      </el-button>
                    <el-button :type="status=='none'?'primary':''" @click="resetForm('searchform')" size="mini">
                      
                    </el-button>
                  </span>
                </div>
              </el-col>
            </el-row>

          </el-form>
        </div>
        

        <!--  -->
        <div class="table-con" id="app">
          <el-table :data="tableData" v-loading="listLoading" size="mini"
          border style="width: 100%" height="385px">

            <el-table-column prop="channelName" label="" align="center">
              <!--  -->
            <template slot-scope="scope">
              <el-button type="text" size="small" 
              @click="gouserdetail(scope.row.processNo)">
                {{scope.row.channelName}}</el-button>
            </template>
            </el-table-column>

            <el-table-column prop="name" label="" width="100" align="center">
            <!--  -->
            <template slot-scope="scope">
              <el-button type="text" size="small" 
              @click="gouserdetail(scope.row.processNo,scope.row.channelName,scope.row.productName)">
                {{scope.row.name}}</el-button>
            </template>
            </el-table-column>

            <el-table-column prop="channelName" label="" width="90" align="center">
            </el-table-column>
            <el-table-column prop="productName" label="" width="90" align="center">
            </el-table-column>
            <el-table-column prop="actualPrin" label="" width="90" align="center">
            </el-table-column>
            <el-table-column prop="actualInt" label="" width="90" align="center">
            </el-table-column>
            <el-table-column prop="repayAmt" label="" width="90" align="center">
            </el-table-column>
            <el-table-column prop="repayType" label="" width="90" align="center">
            </el-table-column>
            <el-table-column prop="statementType" label="" align="center">
              <!--  -->
            <template slot-scope="scope">
              <el-button type="text" size="small" 
              @click="gouserdetail(scope.row.processNoe)">
                </el-button>
                <el-button type="text" size="small" 
              @click="gouserdetail(scope.row.processNo)">
                </el-button>
            </template>
            </el-table-column>
          </el-table>

          <div class="block">
            <el-pagination background style="text-align:center"
            @size-change="handleSizeChange" 
            @current-change="handleCurrentChange" 
            :current-page="this.searchform.pageIndex" 
            :page-sizes="[20,50,100]" 
            :page-size="this.searchform.pageSize" 
            layout="total, sizes, prev, pager, next" 
            :total="count">
            </el-pagination>
          </div>

        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {formatDate} from '../../common/date.js';

// xlsx
import FileSaver from 'file-saver'
import XLSX from 'xlsx'

//vuex
import {mapState} from 'vuex'

export default {
  data() {
    return {
      userName: "",
      listLoading: false,
      status: "none",
      allmoney:0,

      //
      channellist:[],

      //
      productlist:[],

      //
      payChannellist:[],

      count:0,
      pageIndex:1,//
      pageSize: 50,//

      //
      tableData: [
        {statementType:"j"}
        ],

      searchform: {
        name: "",//
        channelCd: "",//
        productCd: "",//
        repayType:"",//
        repayType:"",//
        beginDate: "",//
        endDate: "",//
        pageIndex: 1,
        pageSize: 50,
      },
    };
  },
  mounted() {
    this.getName();

    this.getchannellist();//

    // this.getproductlist();//

    this.getpayChannellist();//

    this.getlist();
  },
  methods: {
    //
    togglemenu(){
        this.$store.state.isLeftHiden=!this.$store.state.isLeftHiden;
        console.log(this.$store.state.isLeftHiden)
    },

     //
    selChange(val){
      let data = {
        productCd: this.searchform.productCd
      };
      this.$http
        .post(this.$store.state.domain +"/loanManage/product/query", data)
          .then(
          //
          response => {
            if (response.data.code == 0) { 
                let productlists = response.data.detail.result;
                //
                for (var vals of productlists){
                  if (val == vals.channelCd){
                    this.productlist=vals.list;
                  }
                }
            } 
            //
            else {
              this.$message({
                message: response.data.msg,
                type: "error"
              });
            }
          },
          //
          response => {
            // console.log(response);
          }
        );
    },

    // 
      getchannellist() {
      let data = {
        channelCd: this.searchform.channelCd
      };
      this.$http
        .post(this.$store.state.domain +"/loanManage/channel/query", data)
          .then(
          //
          response => {
            if (response.data.code == 0) { 
                this.channellist = response.data.detail.result;
            } 
            //
            else {
              this.$message({
                message: response.data.msg,
                type: "error"
              });
            }
          },
          //
          response => {
            // console.log(response);
          }
        );
      },

    //
      getproductlist(){
      let data = {
        productCd: this.searchform.productCd
      };
      this.$http
        .post(this.$store.state.domain +"/loanManage/product/query", data)
          .then(
          //
          response => {
            if (response.data.code == 0) { 
                let productlists = response.data.detail.result;
                //
                for (var vals of productlists){
                  for(var x of vals.list){
                    this.productlist.push(x);
                  }
                }
            } 
            //
            else {
              this.$message({
                message: response.data.msg,
                type: "error"
              });
            }
          },
          //
          response => {
            // console.log(response);
          }
        );
      },

    //
    getpayChannellist(){
      let data = {
        payChannel: this.searchform.payChannel
      };
      this.$http
        .post(this.$store.state.domain +"/loanManage/getRepayType", data)
          .then(
          //
          response => {
            if (response.data.code == 0) { 
                this.payChannellist = response.data.detail.result;
            } 
            //
            else {
              this.$message({
                message: response.data.msg,
                type: "error"
              });
            }
          },
          //
          response => {
            // console.log(response);
          }
        );
    },

        //excel
        exportExcel () {
            /* generate workbook object from table */
            var wb = XLSX.utils.table_to_book(document.querySelector('#app'))
            /* get binary string as output */
            var wbout = XLSX.write(wb, { bookType: 'xlsx', bookSST: true, type: 'array' })
            try {
                FileSaver.saveAs(new Blob([wbout], { type: 'application/octet-stream' }), '.xlsx')
            } catch (e) { if (typeof console !== 'undefined') console.log(e, wbout) }
            return wbout
        },
        getName() {
          let userName = sessionStorage.getItem("name");
          this.userName = userName;
        },
        goBack() {
          var _this = this;
            this.$confirm('?', '', {
              //type: 'warning'
            }).then(() => {
              sessionStorage.removeItem('name');
              this.$router.push('/login');
            }).catch(() => {

            });
        },
       
        search() {
          this.getlist();
        },

        resetForm(formName) {
          this.$refs[formName].resetFields();
          this.productlist="";
          this.getlist();
        },

        handleSizeChange(psize) {
        this.searchform.pageSize = psize;
        // 
        this.searchform.pageIndex =1;
          this.getlist();
        },

        handleCurrentChange(pindex) {
        this.searchform.pageIndex = pindex;
          this.getlist();
        },

        gouserdetail(processNo,channelName,productName) {
              //
            if(channelName=="" && productName=="001"){
                this.$router.push("/users/detailwanshang?processNo=" + processNo);
            }
            else if(channelName=="" && productName=="001"){
                this.$router.push("/users/detaillinyi?processNo=" + processNo);
            }
        },
        
        getlist() {
          this.$http
            .post(
              this.$store.state.domain +"/loanManage/loanRepay", this.searchform
            )
            .then(
              response => {
                if (response.data.code == 0) {
                //
                this.tableData = response.data.detail.result.pageList;
                this.count = response.data.detail.result.count;
                this.pageSize= response.data.detail.result.pageSize;
                this.pageIndex =response.data.detail.result.pageIndex;
                // this.allmoney = response.data.detail.result.params.repayAmtTotal;
                this.listLoading = false;

                if(this.tableData==null)
                {
                  this.$notify({
                    message: '',
                    type: 'warning',
                    duration:"2000"//
                  });
                }
                } else {
                  this.listLoading=true;
                  this.$message.error(response.data.msg);
                }
              },
              response => {
                //console.log(response);
              }
            );
        }
      },
      filters: {
            formatDate(time) {
                var date = new Date(time);
                return formatDate(date, 'yyyy-MM-dd hh:mm');
            }
      },
      watch: {},
      components: {}
};
</script>

 <style lang='less'>
@import '../users/index.css';

</style>



