<template>
  <div>

    <div class="el-table">
      <table width="100%" >
        <tr colspan="6" >
          <th colspan="6" >
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-hetong"></use>
            </svg>
          </th>
        </tr>
        <ul>
          <li>
            <label ></label>{{detail.custName}}
          </li>
          <li>
            <label ></label>{{detail.phone}}</li>
          <li>
            <label ></label>{{detail.sex}}</li>
          <li>
            <label ></label>{{detail.age}}</li>
          <li>
            <label ></label>{{detail.applyLimit}}</li>
          <li>
            <label ></label>{{detail.applyTerm}}{{detail.month}}</li>
          <li>
            <label ></label>{{detail.goods}}</li>
          <li>
            <label ></label>{{detail.goodsMoney}}</li>
          <li>
            <label ></label>{{detail.occupation}}</li>
          <li>
            <label ></label>{{detail.monthSalary}}</li>
          <li>
            <label ></label>{{detail.empName}}</li>
        </ul>
      </table>
    </div>

    <div class="el-table el-table--fit  el-table--enable-row-hover el-table--enable-row-transition">
      <table width="100%">
        <tr colspan="6">
          <th colspan="6">
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-huankuan"></use>
            </svg></th>
        </tr>
        <ul>
          <li>
            <label ></label>{{detail.cardNo}}</li>
          <li>
            <label ></label>{{detail.bankName}}</li>
          <li>
            <label ></label>{{detail.bankMobile}}</li>
          <li>
            <label ></label>{{detail.cardResult}}</li>
          <li>
            <label ></label>{{detail.cardResult}}</li>
          <li>
            <label ></label>{{detail.cardResult}}</li>
        </ul>
      </table>
    </div>

    <div class="el-table el-table--fit  el-table--enable-row-hover el-table--enable-row-transition">
      <table width="100%">
        <tr colspan="6">
          <th colspan="6">
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-yuqi"></use>
            </svg></th>
        </tr>
        <ul class="place">
          <li>
            <label ></label>{{detail.preApproveResult}}</li>
          <li>
            <label ></label>{{detail.preApproveMoney}}</li>
          <li>
            <label ></label>{{detail.preApproveRepayMethod}}</li>
          <li>
            <label ></label>{{detail.preApproveMonthRate}}</li>
          <li>
            <label ></label>{{detail.preApproveTerm}}</li>
          <li>
            <label ></label>{{detail.preApproveRefuseReason}}</li>  
        </ul>
      </table>
    </div>

    <div class="el-table el-table--fit  el-table--enable-row-hover el-table--enable-row-transition">
      <table width="100%">
        <tr colspan="6">
          <th colspan="6">
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-jihua"></use>
            </svg></th>
        </tr>
           <el-table :data="this.detail.contactList" size="mini" border style="color:blue; font-size:8px">
                <el-table-column prop="index" label="" align="center">
                </el-table-column>
                <el-table-column prop="contactPhone" label="" align="center">
                </el-table-column>
                <el-table-column prop="contactRelationship" label="" align="center">
                </el-table-column>
                <el-table-column prop="remark" label="" align="center">
                </el-table-column>
                <el-table-column prop="remark" label="" align="center">
                </el-table-column>
                <el-table-column prop="remark" label="" align="center">
                </el-table-column>
                <el-table-column prop="remark" label="" align="center">
                </el-table-column>
                <el-table-column prop="remark" label="" align="center">
                </el-table-column>
            </el-table>
      </table>
    </div>

  </div>
</template>
<script>
export default {
  data() {
    return {

      //
      detail: {},
    };
  },
  mounted() {
    this.getdetail();
  },
  methods: {
    
    getdetail() {
      let data={
        processNo:this.$route.query.processNo
      };
      this.$http
        .post(this.$store.state.domain +"/loanManage/custInfo",data)
        .then(
          response => {
            if (response.data.code == 0) {
              this.detail = response.data.detail.result;
            }
          },
          response => {
            //console.log(response);
          }
        );
    }
  },
  watch: {},
  components: {}
};
</script>

 <style lang='less' scoped>
// @import "index.less";
* {
  font-size: 14px;
  list-style: none;
  box-sizing: border-box;
}
table {
  border: none;
}
ul {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  // 
  margin: 10px 0 10px;
  li {
    width: 40%;
    height: 40px;
    line-height: 40px;
    label {
      width: 130px;
      margin-right: 10px;
      text-align: right;
      float: left;
      color: #b48e3d;
    }
  }
}
.outpadding {
  padding: 30px 0px;
}
svg {
  padding: -1px 8px !important;
}
</style>



