<template>
<!-- truefalse -->
  <div v-if="userName==this.$store.state.nameler?false:true">
    <div class="topBanner">
      <el-tooltip class="item" effect="light" content="" placement="bottom">
      <p class='tog' @click="togglemenu">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-zhankai1"></use>
        </svg></p>
      </el-tooltip>

      <p class='left'> >> </p>

      <p class='right' @click="goBack">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-shouye"></use>
        </svg></p>

      <p class='right'>
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-character"></use>
        </svg>{{userName}}</p>

      <p class='right' @click="screenfull">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-quanping4"></use>
      </svg></p>

      <p class='right' title="">
      <router-link to="/yonghuguanli/mimaxiugai" class="roulink">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-mimazhongzhi"></use>
      </svg>
      </router-link>
      </p>

      <p class='right' @click="lastpage">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-fanhui2"></use>
      </svg></p>
    </div>

<div class="content">
    <div class="searcharea">
          <!-- el-formmodelel-form-itemel-form-itemprop -->
          <el-form ref="searchform" :model="searchform" label-width="100%" size="mini">
            <el-row :gutter="24">
              <el-col :span="6">
                  <el-form-item label="" prop="channelCode">
                    <el-select v-model="searchform.channelCode" placeholder="" clearable>
                      <!--  -->
                    <el-option v-for="item in channellist"
                              :label="item.channelCode"
                              :key="item.channelCode"
                              :value="item.channelCode"
                          ></el-option>
                  </el-select>
                  </el-form-item>
              </el-col>
              
               <el-col :span="6">
                <el-form-item label="" prop="beginDate">
                    <el-date-picker class="shi" clearable
                      v-model="searchform.beginDate"
                      value-format="yyyy-MM-dd HH:mm:ss"
                      type="datetime"
                      placeholder=""
                      default-time="00:00:00">
                    </el-date-picker>
                </el-form-item>
              </el-col>

              <el-col :span="6">
                 <el-form-item label="" prop="endDate">
                    <el-date-picker class="shi" clearable
                      v-model="searchform.endDate"
                      value-format="yyyy-MM-dd HH:mm:ss"
                      type="datetime"
                      placeholder=""
                      default-time="23:59:59">
                    </el-date-picker>
                </el-form-item>
              </el-col>
            </el-row>

            <el-row :gutter="24">
              <el-col :span="24">
                <div class="di">
                  <div class="wrapper">
                    <el-button type="primary" plain @click="search()" size="mini">
                      </el-button>
                      <el-button type="primary" plain @click="resetForm('searchform')" size="mini">
                      
                    </el-button>
                    <el-button type="primary" plain @click="getdialog()" size="mini">
                      
                    </el-button>
                  </div>
                </div>
              </el-col>
            </el-row>
          </el-form>
        </div>
       
       <!--  -->
       <el-dialog title="" :visible.sync="dialogFormVisible" width="30%" center>
        <el-form :model="searchform">
           <el-form-item label="" prop="channelCode">
                    <el-select v-model="searchform.channelCode" placeholder="" >
                      <!--  -->
                    <el-option v-for="item in channellist" :label="item.channelCode"
                      :key="item.channelCode" :value="item.channelCode"></el-option>
                    </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-date-picker v-model="searchform.settleDate" value-format="yyyy-MM-dd"
                type="date" placeholder="" style="width:140px!important;">
              </el-date-picker>
            </el-form-item>
          </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false"> </el-button>
          <el-button type="primary" @click="shengchengfile()"> </el-button>
        </div>
      </el-dialog>
        <!-- ===============================  =================================== -->
       
          <el-table :data="tableData"  border size="medium" stripe
          v-loading="listLoading" 
          element-loading-text=""
          element-loading-spinner="el-icon-loading"
          element-loading-background="rgba(0, 0, 0, 0.8)"
          style="width: 100%; height:100%;">
            <el-table-column prop="channelCode" label="" align="center">
            </el-table-column>
            <el-table-column prop="fileName" label="" align="center">
            </el-table-column>
            <el-table-column prop="repayTotal" label="" align="center">
            </el-table-column>
            <el-table-column prop="repayNum" label="" width="100px" align="center">
            </el-table-column>
            <el-table-column prop="settleDate" label="" align="center">
            </el-table-column>
            <el-table-column prop="fileUpdateDate" label="" align="center">
            </el-table-column>
            <el-table-column prop="status" label="" width="130px"  align="center">
            </el-table-column>
            <el-table-column  prop="operation" label="" align="center">
              <!--  -->
              <template slot-scope="scope">
                <el-button type="text" size="small"
                 @click="operation(scope.row.channelCode,scope.row.settleDate)">
                  {{scope.row.operation}}</el-button>
              </template>
            </el-table-column>
          </el-table>

          <!--  -->
          <div class="block">
            <el-pagination background style="text-align:center" 
            @size-change="handleSizeChange" 
            @current-change="handleCurrentChange" 
            :current-page="this.searchform.pageIndex"
             :page-sizes="[20,50,100]"  
             :page-size="this.searchform.pageSize" 
             layout="total, sizes, prev, pager, next" 
             :total="count"><!---->
            </el-pagination>
          </div>
</div>
  </div>
</template>
<script>
// 
import {formatDate} from '../../common/date.js';
import screenfull from 'screenfull'


//vuex
import {mapState} from 'vuex'

export default {
  data() {
    return {
      dialogFormVisible: false,//
      
      count:0,//
      pageIndex:1,//
      pageSize: 50,//
      isFullscreen: false,
      loadbutton:false,//
      userName: "",
      status: "none",
      listLoading: false,
      //
      channellist:[],

      //
      tableData: [],
      searchform: {
        channelCode:"",//
        settleDate:"",//
        userId:"",//ID
        pageIndex:1,//
        pageSize: 50,//
      },
    };
  },
  // mounted
  mounted() {
    this.getName();//
    this.getchannellist();//
     window.onresize = () => {
            // ESC
            if (!this.checkFull()) {
              // esc
              this.isFullscreen = false
            }
          };
      
  },
  //html
  created() {
    this.getlist();//
},
  methods: {
    //
    lastpage(){
      window.history.go(-1);
    },
     //
    getdialog(){
      this.dialogFormVisible=true
    },
    //
    shengchengfile(){
    this.dialogFormVisible=false;//
    // 
     this.getfile();
    },
    // pagesize
      handleSizeChange(psize) {
        // 
        this.searchform.pageSize = psize;
        // 
        this.searchform.pageIndex =1;
        this.getlist();
      },

      // currentPage
      handleCurrentChange(pindex) {
        this.searchform.pageIndex = pindex;
        this.getlist();
      },
    //
    getfile(){
      if(this.searchform.channelCode=="" && this.searchform.settleDate==""){
        this.$alert('', '', {
          confirmButtonText: '',
          callback: action => {
            //
          this.dialogFormVisible=true
          }
        });
      }
      else if(this.searchform.channelCode=="" || this.searchform.settleDate==""){
        this.$alert('', '', {
          confirmButtonText: '',
          callback: action => {
            //
          this.dialogFormVisible=true
          }
        });
      }
      else{
          this.listLoading=true;
          this.searchform.userId = sessionStorage.getItem("userId");
          this.$http
            .post(
            this.$store.state.domain +"/loanManage/generateSettleFile", this.searchform 
            )
            .then(  
            // Lambda
            response => {
              //
                if (response.data.code == 0) {
                    this.$confirm(response.data.detail.result, response.data.msg, {
                    confirmButtonText: '',
                    showCancelButton:false
                }).then(() => {
                    // 
                      this.getlist();     
                  }).catch(() => {
                    this.$message({
                      type: 'warning',
                      message: '!'
                    });          
                  });
              }
              //  
              else if(response.data.code == 1){
                this.$alert(response.data.msg, '', {
                    confirmButtonText: '',
                    callback: action => {
                      this.getlist();
                    }
                });
              }
                
            },
            response => {
                //
                this.listLoading=false;
                this.$message({
                    dangerouslyUseHTMLString: true,//html
                    message: '<svg class="icon" aria-hidden="true"> <use xlink:href="#icon-shengqi"></use> </svg> '+
                    response.body.message,
                    type: "error"
                    });
                console.log(response);
            }
            );
            
          }
    },

    
    //
    operation(channelCode,settleDate){
      //
       this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
             //channelCodesearchform
              this.searchform.channelCode=channelCode;
              this.searchform.settleDate=settleDate;
              this.searchform.userId = sessionStorage.getItem("userId");
              this.$http
                .post(
                this.$store.state.domain +"/loanManage/settleOperate", this.searchform 
                )
                .then(  
                // Lambda
                response => {
                  //
                    if (response.data.code == 0) {
                        this.$alert(response.data.detail.result, response.data.msg, {
                        confirmButtonText: '',
                        callback: action => {
                          // 
                          this.searchform.channelCode=""
                          this.getlist();
                        }
                    });
                  }
                  //  
                  else if(response.data.code == 1){
                    this.$alert(response.data.msg, '', {
                        confirmButtonText: '',
                        callback: action => {
                          this.searchform.channelCode=""
                          this.getlist();
                        }
                    });
                  }
                    
                },
                response => {
                    //
                    this.listLoading=false;
                    this.$message({
                        dangerouslyUseHTMLString: true,//html
                        message: '<svg class="icon" aria-hidden="true"> <use xlink:href="#icon-shengqi"></use> </svg> '+
                        response.body.message,
                        type: "error"
                        });
                    console.log(response);
                }
                );
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });          
        });
     
    },
    //
    getdate() {
          var now = new Date(),
              y = now.getFullYear(),
              m = now.getMonth() + 1,
              d = now.getDate();
          return y + "-" + (m < 10 ? "0" + m : m) + "-" + (d < 10 ? "0" + d : d) + " " + now.toTimeString().substr(0, 8);
      },
    /**
     * 
     */
    screenfull() {
      if (!screenfull.enabled) {
        this.$message({
          message: 'Your browser does not work',
          type: 'warning'
        })
        return false
      }
      screenfull.toggle()
      this.isFullscreen = true
    },
    /**
     * ESC
     */
    checkFull() {
      var isFull = document.fullscreenEnabled || window.fullScreen || document.webkitIsFullScreen || document.msFullscreenEnabled
      // to fix : false || undefined == undefined
      if (isFull === undefined) {
          isFull = false
      }
      return isFull
    },

    //
    togglemenu(){
        this.$store.state.isLeftHiden=!this.$store.state.isLeftHiden;
        console.log(this.$store.state.isLeftHiden)
    },

    //vue  sessionStorage
    getName() {
    let userName = sessionStorage.getItem("name");
    this.userName = userName;
    },
    // 
    goBack() {
    var _this = this;
            this.$confirm('?', '', {
                //type: 'warning'
            }).then(() => {
        window.sessionStorage.removeItem('name');//sessionname
                this.$router.push('/login');
            }).catch(() => {

            });
    },
    
    // 
    search() {
      this.getlist();
    },

    // 
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },

    // 
    getchannellist() {
    let data = {
      channelCd: this.searchform.channelCd
    };
    this.$http
      .post(this.$store.state.domain +"/loanManage/channel/query", data)
        .then(
        //
        response => {
          if (response.data.code == 0) { 
              this.channellist = response.data.detail.result;
          } 
          //
          else {
            this.$message({
              message: response.data.msg,
              type: "error"
            });
          }
        },
        //
        response => {
          // console.log(response);
        }
      );
    },

    // ajaxVue  this.$http  HTTP 
    getlist() {
    this.listLoading=true;
    this.$http
        .post(
        this.$store.state.domain +"/loanManage/getNormalSettle", this.searchform 
        )
        //then()then()then()
        .then(  
        // Lambda
        response => {
            if (response.data.code == 0) {
            //
            this.tableData = response.data.detail.result.pageList;
            this.pageSize= response.data.detail.result.pageSize;
            this.pageIndex=response.data.detail.result.pageIndex;
            this.count= response.data.detail.result.count;
            this.listLoading=false;

            if(this.tableData==null)
            {
                this.$notify({
                message: '',
                type: 'warning',
                duration:"2000"//
                });
            }
            } else {
            //   this.listLoading=true;
            // //
            //   this.$message({
            //       message: response.data.msg,
            //       type: "error"
            //     });
            }
            
        },
        response => {
            //
            this.listLoading=false;
            this.$message({
                dangerouslyUseHTMLString: true,//html
                message: '<svg class="icon" aria-hidden="true"> <use xlink:href="#icon-shengqi"></use> </svg> '+
                response.body.message,
                type: "error"
                });
            console.log(response);
        }
        );
    },
  },

    //
    filters: {
            formatDate(time) {
                var date = new Date(time);
                return formatDate(date, 'yyyy-MM-dd hh:mm');
            }
    },
    
};
</script>

 <style lang='less'>

//  less
@import '../users/index.css';






































































































































































































































































































































</style>



