<template>
  <div v-if="userName=='config'?false:true">
    <div class="topBanner">
      
      <el-tooltip class="item" effect="dark" content="" placement="bottom">
      <p class='tog' @click="togglemenu">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-zhankai1"></use>
        </svg></p>
      </el-tooltip>

      <p class='left'></p>

      <el-tooltip class="item" effect="dark" content="" placement="bottom">
      <p class='right' @click="goBack">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-shouye"></use>
        </svg></p>
      </el-tooltip>

      <el-tooltip class="item" effect="dark" content="" placement="bottom">
      <p class='right' title="">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-character"></use>
        </svg>{{userName}}</p>
      </el-tooltip>
      <p class='right' @click="screenfull">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-quanping4"></use>
      </svg></p>

    </div>

        <div class="searcharea">
          <el-form ref="searchform" :model="searchform" label-width="100%" size="mini">
            <el-row :gutter="30">
              <el-col :span="6">
                <div class="grid-content bg-purple">
                  <el-form-item label="" prop="name">
                    <el-input v-model="searchform.name"></el-input>
                  </el-form-item>
                </div>
              </el-col>

              <el-col :span="6">
                <div class="grid-content bg-purple">
                  <el-form-item label="" prop="channelCd">
                  <el-select v-model="searchform.channelCd" placeholder=""
                  @change="selChange($event)">
                      <!--  -->
                    <el-option v-for="item in channellist"
                              :label="item.channelCode"
                              :key="item.channelCode"
                              :value="item.channelCode"
                          ></el-option>
                  </el-select>
                  </el-form-item>
                </div>
              </el-col>

              <el-col :span="6">
                <div class="grid-content bg-purple">
                  <el-form-item label="" prop="productCd">
                  <el-select v-model="searchform.productCd" placeholder="">
                    <!--  -->
                    <el-option v-for="item in productlist"
                              :label="item.productCode"
                              :key="item.productCode"
                              :value="item.productCode"
                          ></el-option>
                  </el-select>
                  </el-form-item>
                </div>
              </el-col>
            </el-row>
            <el-row :gutter="30">
              <el-col :span="6">
                <el-form-item label="" prop="startDate">
                  <div class="grid-content bg-purple">
                    <el-date-picker v-model="searchform.beginDate"  class="shi"
                    value-format="yyyy-MM-dd HH:mm:ss" type="date" placeholder="">
                    </el-date-picker>
                  </div>
                </el-form-item>
              </el-col>

              <el-col :span="6">
                <el-form-item label="" prop="endDate">
                  <el-date-picker v-model="searchform.endDate"  class="shi"
                  value-format="yyyy-MM-dd HH:mm:ss" type="date" placeholder="">
                  </el-date-picker>
                </el-form-item>
              </el-col>

              <el-col :span="12">
                <div class="di">
                  <span class="wrapper">
                    <el-button :type="status=='none'?'primary':''" @click="search()" size="mini">
                      </el-button>
                    <el-button :type="status=='done'?'primary':''" @click="resetForm('searchform')" size="mini">
                      
                    </el-button>
                  </span>
                </div>
              </el-col>
            </el-row>
          </el-form>
        </div>
        
        <div class="cen-box">
        <div class="dl-center">
          <el-col :span="6">
                 <el-tag type="seccess" size="mini">{{allmoney}}</el-tag>
          </el-col>
        </div>
        </div>

          <el-table :data="tableData" border stripe
          v-loading="listLoading" size="medium"
          element-loading-text="" style="width: 100%;height:100%;"
          element-loading-spinner="el-icon-loading"
          element-loading-background="rgba(0, 0, 0, 0.8)" >
            <el-table-column prop="name" label="" align="center">
              <template slot-scope="scope">
                <el-button type="text" size="small" @click="gouserdetail(scope.row.loan_id)">
                  {{scope.row.name}}</el-button>
              </template>
            </el-table-column>

            <el-table-column prop="channelCd" label="" align="center">
            </el-table-column>
            <el-table-column prop="productCd" label="" align="center">
            </el-table-column>
            <el-table-column prop="payAmt" label="" align="center">
            </el-table-column>
            <el-table-column prop="way_return" label="" align="center">
            </el-table-column>
            <el-table-column prop="loan_total" label="" align="center">
            </el-table-column>
            <el-table-column prop="loanExpireDate" label="" align="center">
            </el-table-column>

            <!--  -->
            <el-table-column prop="loanstate" label="" align="center">
              <template slot-scope="scope">
                <span v-if="scope.row.loanstate == '1'"></span>
                <span v-else-if="scope.row.loanstate == '2'"></span>
              </template>
            </el-table-column>

            <el-table-column prop="" label="" align="center">
                <template slot-scope="scope">
                  <!-- scope.loanstate -->
                    <el-button size="small" @click="handleEdit(scope.row)">
                      </el-button>
                </template>
            </el-table-column>
          </el-table>

          <div class="block">
            <el-pagination background style="text-align:center" 
            @size-change="handleSizeChange" 
            @current-change="handleCurrentChange" 
            :current-page="pageIndex" 
            :page-sizes="[20,50,100]" 
            :page-size="pageSize" 
            layout="total, sizes, prev, pager, next" 
            :total="tableData.length">
            </el-pagination>
          </div>

     <!---->
          <el-dialog  title="" :visible.sync="editFormVisible" 
          :close-on-click-modal="false" width="30%">
          <div class="el-dia">
            <el-form :model="changeform" ref="changeform" >
              <el-form-item label="" prop="loanstate" >
                  <el-select v-model="changeform.loanstate" placeholder="">
                    <el-option label="" value="1"></el-option>
                    <el-option label="" value="2"></el-option>
                  </el-select>
              </el-form-item>
              </el-form>
          </div>
              <div slot="footer" class="dialog-footer">
                <el-button @click="editFormVisible = false"> </el-button>
                <el-button type="primary" @click="editSubmit()"> </el-button>
              </div>
           </el-dialog>
  </div>
</template>

<script>
import {formatDate} from '../../common/date.js';
import screenfull from 'screenfull'

//vuex
import {mapState} from 'vuex'

export default {
  data() {
    return {
      isFullscreen: false,
      userName: "",
      status: "none",
      listLoading: false,
      pageIndex:1,//
      pageSize: 50,//
      editFormVisible: false,//
      allmoney:0,
      
      //
      channellist:[],

      //
      productlist:[],

      tableData: [
      ],
      //
      searchform: {
        name: "",
        channelCd: "",//
        productCd: "",//
        payAmt:"",//
        way_return:"",//
        loan_total:"",//
        loanExpireDate: "",//
        loanstate:"",//
        pageIndex: "1",
        pageSize: 50,
      },

      //
      changeform: {
        name: "",
        channelCd: "",//
        productCd: "",//
        payAmt:"",//
        way_return:"",//
        loan_total:"",//
        loanExpireDate: "",//
        loanstate:"",//
      },
      
    };
  },
  mounted() {
     window.onresize = () => {
            // ESC
            if (!this.checkFull()) {
              // esc
              this.isFullscreen = false
            }
          };
    this.getName();
    this.getchannellist();//

    this.getproductlist();//
    // this.getlist();
  },
  methods: {
    /**
     * 
     */
    screenfull() {
      if (!screenfull.enabled) {
        this.$message({
          message: 'Your browser does not work',
          type: 'warning'
        })
        return false
      }
      screenfull.toggle()
      this.isFullscreen = true
    },
    /**
     * ESC
     */
    checkFull() {
      var isFull = document.fullscreenEnabled || window.fullScreen || document.webkitIsFullScreen || document.msFullscreenEnabled
      // to fix : false || undefined == undefined
      if (isFull === undefined) {
          isFull = false
      }
      return isFull
    },

    //
    togglemenu(){
        this.$store.state.isLeftHiden=!this.$store.state.isLeftHiden;
        console.log(this.$store.state.isLeftHiden)
    },
    
    //
    selChange(val){
      let data = {
        productCd: this.searchform.productCd
      };
      this.$http
        .post(this.$store.state.domain +"/loanManage/product/query", data)
          .then(
          //
          response => {
            if (response.data.code == 0) { 
                let productlists = response.data.detail.result;
                //
                for (var vals of productlists){
                  if (val == vals.channelCd){
                    this.productlist=vals.list;
                  }
                }
            } 
            //
            else {
              this.$message({
                message: response.data.msg,
                type: "error"
              });
            }
          },
          //
          response => {
            // console.log(response);
          }
        );
    },

    // 
      getchannellist() {
      let data = {
        channelCd: this.searchform.channelCd
      };
      this.$http
        .post(this.$store.state.domain +"/loanManage/channel/query", data)
          .then(
          //
          response => {
            if (response.data.code == 0) { 
                this.channellist = response.data.detail.result;
            } 
            //
            else {
              this.$message({
                message: response.data.msg,
                type: "error"
              });
            }
          },
          //
          response => {
            // console.log(response);
          }
        );
      },

    //
      getproductlist(){
      let data = {
        productCd: this.searchform.productCd
      };
      this.$http
        .post(this.$store.state.domain +"/loanManage/product/query", data)
          .then(
          //
          response => {
            if (response.data.code == 0) { 
                let productlists = response.data.detail.result;
                //
                for (var vals of productlists){
                  for(var x of vals.list){
                    this.productlist.push(x);
                  }
                }
            } 
            //
            else {
              this.$message({
                message: response.data.msg,
                type: "error"
              });
            }
          },
          //
          response => {
            // console.log(response);
          }
        );
      },

    //
			handleEdit(rows) {
        this.editFormVisible = true;
        this.changeform = Object.assign({}, rows);//
      },
      //
			editSubmit() {
        let tableList=this.changeform;
				this.$refs.changeform.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.editLoading = true;
							let para = Object.assign({}, this.changeform);
							editUser(para).then((res) => {
								this.editLoading = false;
								this.$message({
									message: '',
									type: 'success'
								});
								this.$refs['changeform'].resetFields();
								this.editFormVisible = false;
								this.getlist();
							});
						});
					}
				});
			},

    getName() {
      let userName = sessionStorage.getItem("name");
      this.userName = userName;
    },
    goBack() {
      var _this = this;
				this.$confirm('?', '', {
					//type: 'warning'
				}).then(() => {
					sessionStorage.removeItem('name');
					this.$router.push('/login');
				}).catch(() => {

				});
    },
    changepage(val) {
      this.searchform.pageIndex = val;
      this.getlist();
    },
    search() {
      this.searchform.pageIndex = 1;
      this.listLoading = true;//
      this.getlist();
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
      this.productlist="";
    },
    // pagesize
    handleSizeChange(psize) {
      this.pageSize = psize;
      this.getlist();
    },
    // currentPage
    handleCurrentChange(pindex) {
      this.searchform.pageIndex = pindex;
      this.getlist();
    },
    gouserdetail(loan_id) {
      this.$router.push("/users/detail?loan_id=" + loan_id);
    },
    getlist() {
      this.$http
        .get(
          this.$store.state.domain +"/loanManage/loanPay",  //url
          {
            params: this.searchform
          }
        )
        .then(
          response => {
            if (response.data.code == 0) {
              //
            //tableData
            this.tableData = response.data.detail.resultList;
            //total_count
            this.total_count = response.data.detail.count;
            // this.allmoney = response.data.detail.totalMoney;
            } else {
              this.$message.error(response.data.msg);
            }
          },
          response => {
            //console.log(response);
          }
        );
    }
  },
  filters: {
        formatDate(time) {
            var date = new Date(time);
            return formatDate(date, 'yyyy-MM-dd hh:mm');
        }
  },
  watch: {},
  components: {}
};
</script>

 <style lang='less'>

//  less
@import '../users/index.css';


</style>



