<template>
  <div>
    
      <el-tooltip class="item" effect="light" content="" placement="bottom">
      <p class='tog' @click="togglemenu">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-zhankai1"></use>
        </svg></p>
      </el-tooltip>
      <p class='right' @click="screenfull">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-quanping4"></use>
      </svg></p>
      
      <p class='right' @click="lastpage">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-fanhui2"></use>
      </svg></p>
    <div class="outpadding">
          <!-- <p class="xiangqingtitle"><i class="el-icon-location-outline"></i></p> -->

          <el-tabs v-model="tabname" type="card">
            <!--  -->
            <el-tab-pane label="" name="userbaselinyi"></el-tab-pane>
            <el-tab-pane label="" name="userpics"></el-tab-pane>
          </el-tabs>
          <userbaselinyi v-if="tabname=='userbaselinyi'"></userbaselinyi>
          <userpics v-if="tabname=='userpics'"></userpics>
    </div>
  </div>
</template>
<script>
import userbaselinyi from "./userbaselinyi.vue";
import userpics from "./userpics.vue";
import screenfull from 'screenfull'

export default {
  data() {
    return {
      isFullscreen: false,
      detail: {},
      tabname: "userbaselinyi",//
      // caseId: this.$route.query.caseId
    };
  },
  mounted() {
    window.onresize = () => {
            // ESC
            if (!this.checkFull()) {
              // esc
              this.isFullscreen = false
            }
          };
  },
  methods: {
    //
    lastpage(){
      window.history.go(-1);
    },
    /**
     * 
     */
    screenfull() {
      if (!screenfull.enabled) {
        this.$message({
          message: 'Your browser does not work',
          type: 'warning'
        })
        return false
      }
      screenfull.toggle()
      this.isFullscreen = true
    },
    /**
     * ESC
     */
    checkFull() {
      var isFull = document.fullscreenEnabled || window.fullScreen || document.webkitIsFullScreen || document.msFullscreenEnabled
      // to fix : false || undefined == undefined
      if (isFull === undefined) {
          isFull = false
      }
      return isFull
    },

    //
    togglemenu(){
        this.$store.state.isLeftHiden=!this.$store.state.isLeftHiden;
        console.log(this.$store.state.isLeftHiden)
    },
  },
  watch: {},
  components: {
    userbaselinyi,
    userpics
  }
};
</script>

 <style lang='less'scoped>
@import '../users/index.css';

.outpadding {
  padding: 30px 20px;

  background-color: rgb(252, 249, 249);
}

</style>



