<template>
<!-- truefalse -->
  <div v-if="userName==this.$store.state.nameler?false:true">
    <div class="topBanner">
      <el-tooltip class="item" effect="light" content="" placement="bottom">
      <p class='tog' @click="togglemenu">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-zhankai1"></use>
        </svg></p>
      </el-tooltip>

      <p class='left'> >> </p>

      <p class='right' @click="goBack">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-shouye"></use>
        </svg></p>

      <p class='right'>
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-character"></use>
        </svg>{{userName}}</p>

      <p class='right' @click="screenfull">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-quanping4"></use>
      </svg></p>

      <p class='right' title="">
      <router-link to="/yonghuguanli/mimaxiugai" class="roulink">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-mimazhongzhi"></use>
      </svg>
      </router-link>
      </p>

      <p class='right' @click="lastpage">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-fanhui2"></use>
      </svg></p>
    </div>

    <div class="content">
        <div class="searcharea">
          <!-- el-formmodelel-form-itemel-form-itemprop -->
          <el-form ref="searchform" :model="searchform" label-width="100%" size="mini">
            <el-row :gutter="30">
              <el-col :span="6">
                  <el-form-item label="" prop="name">
                    <el-input v-model="searchform.name" clearable></el-input>
                  </el-form-item>
              </el-col>
              <el-col :span="6">
                  <el-form-item label="" prop="channelCd">
                    <el-select v-model="searchform.channelCd" placeholder="" 
                    @change="selChange($event)" clearable>
                      <!--  -->
                    <el-option v-for="item in channellist"
                              :label="item.channelCode"
                              :key="item.channelCode"
                              :value="item.channelCode"
                          ></el-option>
                  </el-select>
                  </el-form-item>
              </el-col>
              <el-col :span="6">
                  <el-form-item label="" prop="productCd">
                    <el-select v-model="searchform.productCd" placeholder="" clearable>
                    <!--  -->
                    <el-option v-for="item in productlist"
                              :label="item.productCode"
                              :key="item.productCode"
                              :value="item.productCode"
                          ></el-option>
                  </el-select>
                  </el-form-item>
              </el-col>
              <el-col :span="6">
                  <el-form-item label="" prop="payAmt">
                    <el-select v-model="searchform.payAmt" placeholder="" clearable>
                      <el-option v-for="item in payamts"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value"></el-option>
                  </el-select>
                  </el-form-item>
              </el-col>
            </el-row>

            <el-row :gutter="30">
              <el-col :span="6">
                  <el-form-item label="" prop="intRate">
                    <el-select v-model="searchform.intRate" placeholder="" clearable>
                    <el-option v-for="item in rates"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value"></el-option>
                  </el-select>
                  </el-form-item>
              </el-col>
              <el-col :span="6">
                  <el-form-item label="" prop="term">
                    <el-select v-model="searchform.term" placeholder="" clearable>
                      <el-option v-for="item in terms"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value"></el-option>
                  </el-select>
                  </el-form-item>
              </el-col>

              <el-col :span="6">
                  <el-form-item label="" prop="loanType">
                    <el-select v-model="searchform.loanType" placeholder="" clearable>
                    <!--  -->
                    <el-option v-for="item in loantypelist"
                              :label="item.description"
                              :key="item.code"
                              :value="item.code"
                          ></el-option>
                  </el-select>
                  </el-form-item>
              </el-col>

              <el-col :span="6">
                  <el-form-item label="" prop="payOrderStatus">
                    <el-select v-model="searchform.payOrderStatus" placeholder="" clearable>
                    <!--  -->
                    <el-option v-for="item in PayOrderStatusList"
                              :label="item.description"
                              :key="item.code"
                              :value="item.code"
                          ></el-option>
                  </el-select>
                  </el-form-item>
              </el-col>

            </el-row>

            <el-row :gutter="30">
              <el-col :span="6">
                <el-form-item label="" prop="beginDate">
                    <!-- <el-date-picker v-model="searchform.startDate" value-format="yyyy-MM-dd" 
                    type="date" placeholder="">
                    </el-date-picker> -->
                    <el-date-picker class="shi" clearable
                      v-model="searchform.beginDate"
                      value-format="yyyy-MM-dd HH:mm:ss"
                      type="datetime"
                      placeholder=""
                      default-time="00:00:00">
                    </el-date-picker>
                </el-form-item>
              </el-col>

              <el-col :span="6">
                <el-form-item label="" prop="endDate">
                  <!-- <el-date-picker v-model="searchform.endDate" value-format="yyyy-MM-dd" 
                  type="date" placeholder="">
                  </el-date-picker> -->
                  <el-date-picker class="shi" clearable
                      v-model="searchform.endDate"
                      value-format="yyyy-MM-dd HH:mm:ss"
                      type="datetime"
                      placeholder=""
                      default-time="23:59:59">
                    </el-date-picker>
                </el-form-item>
              </el-col>

              <el-col :span="6">
                <div class="di">
                  <div class="wrapper">
                    <el-button type="primary" plain @click="search()" size="mini">
                      </el-button>
                    <el-button type="primary" plain @click="resetForm('searchform')" size="mini">
                      
                    </el-button>
                  </div>
                </div>
              </el-col>
            </el-row>
          </el-form>
        </div>

        <div class="cen-box">
        <div class="dl-center">
          <el-col :span="6">
            <el-tag type="danger" size="mini">{{allmoney}}</el-tag>
          </el-col>
        </div>

        <div class="dr-center">
          <a id="downlink"></a>
          <el-col :span="6">
                <el-button type="success" plain @click="exportExcel('')" size="mini">
                  Excel</el-button>
          </el-col>
        </div>
        </div>

        <!-- ===============================  =================================== -->
       
          <el-table :data="tableData"  border size="medium" stripe
          v-loading="listLoading" 
          element-loading-text=""
          element-loading-spinner="el-icon-loading"
          element-loading-background="rgba(0, 0, 0, 0.8)"
          style="width: 100%; height:100%;">
          <el-table-column prop="borrowTime" label="" align="center">
            </el-table-column>
            <el-table-column prop="processNo" label="" align="center">
            </el-table-column>
            <el-table-column prop="contractNo" label="" align="center">
            </el-table-column>
            <el-table-column prop="name" label="" width="80" align="center">
              <!--  -->
              <template slot-scope="scope">
                <el-button type="text" size="small" 
                @click="gouserdetail(scope.row.processNo,scope.row.channelCode)">
                  {{scope.row.name}}</el-button>
              </template>
            </el-table-column>
            <!-- <el-table-column prop="channelCode" label="" width="100" align="center">
            </el-table-column> -->
            <el-table-column prop="productCode" label="" width="140" align="center">
            </el-table-column>
            <el-table-column prop="payAmt" label="" width="120" align="center">
            </el-table-column>
            <el-table-column prop="intRate" label="" width="100" align="center">
            </el-table-column>
            <el-table-column prop="term" label="" width="80" align="center">
            </el-table-column>
            
            <el-table-column prop="loanType" label="" width="140" align="center">
            </el-table-column>
            <el-table-column prop="payOrderStatus" label="" width="100" align="center">
            </el-table-column>
            <el-table-column prop="loanPmtDueDate" label="" align="center">
            </el-table-column>

          </el-table>

     <!--  -->
          <div class="block">
            <el-pagination background style="text-align:center" 
            @size-change="handleSizeChange" 
            @current-change="handleCurrentChange" 
            :current-page="this.searchform.pageIndex"
             :page-sizes="[20,50,100]"  
             :page-size="this.searchform.pageSize" 
             layout="total, sizes, prev, pager, next" 
             :total="count"><!---->
            </el-pagination>
          </div>
    </div>
  </div>
</template>
<script>
// 
import {formatDate} from '../../common/date.js';
import screenfull from 'screenfull'

// xlsx
import FileSaver from 'file-saver'
import XLSX from 'xlsx'

//vuex
import {mapState} from 'vuex'

export default {
  data() {
    return {
      isFullscreen: false,
      show:false,//

      //
      rates:[{
        value:"A",
        label:"0.83%()"
      },
      {
        value:"B",
        label:"0.83%-1.25%()"
      },
      {
        value:"C",
        label:"1.25%-1.67%()"
      },
      {
        value:"D",
        label:"1.67%-2.08%()"
      },
      {
        value:"E",
        label:"2.08%"
      }],

      //
      payamts:[{
        value:"A",
        label:"5()"
      },
      {
        value:"B",
        label:"5-10()"
      },
      {
        value:"C",
        label:"10-50()"
      },
      {
        value:"D",
        label:"50-100()"
      },
      {
        value:"E",
        label:"100"
      }],

      //
      terms:[{
        value:"A",
        label:"3()"
      },
      {
        value:"B",
        label:"3-6()"
      },
      {
        value:"C",
        label:"6-12()"
      },
      {
        value:"D",
        label:"12"
      }],

      userName: "",
      status: "none",
      listLoading: false,
      pageIndex:1,//
      pageSize: 50,//
      allmoney:0,
      count:0,//

      //
      channellist:[],

      //
      productlist:[],

      //
      loantypelist:[],

      //
      PayOrderStatusList:[],

      //
      tableData: [],

      searchform: {
        // name: "",//
        // channelCd: "",//
        productCd: "",//
        loanType: "",//
        term:"",//
        termBegin:"",//
        termEnd:"",//
        intRate:"",//
        intRateBegin:"",
        intRateEnd:"",
        payChannel:"",//
        payAmt:"",//
        payOrderStatus:"S",//
        payAmtBegin:"",//
        payAmtEnd:"",//
        beginDate: new Date().toLocaleDateString()+" 00:00:00",//
        endDate: new Date().toLocaleDateString()+" 23:59:59",//
        pageIndex: 1,
        pageSize : 50,
      },
      
    };
  },
  // mounted
  mounted() {
    this.getName();//
    this.getchannellist();//

    // this.getproductlist();//

    this.getloantypelist();//

    this.getPayOrderStatusList();//
    this.getRate();//
    this.getTerm();//
    this.getloan();//
     window.onresize = () => {
            // ESC
            if (!this.checkFull()) {
              // esc
              this.isFullscreen = false
            }
          };
  },
  //html
created() {
    this.getlist();//
},
  methods: {
    //
    lastpage(){
      window.history.go(-1);
    },
    // excel
    exportExcel(filename){
        const url = this.$store.state.domain +"/loanManage/loanPayExport";
                this.$http.post(url, this.searchform , {
                    responseType: 'blob'
                }).then(res => {
                    let blob = new Blob([res.data], {
                        type: 'application/vnd.ms-excel,application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'
                    })
                    if (window.navigator.msSaveOrOpenBlob) {
                        navigator.msSaveBlob(blob);
                    } else {
                        let elink = document.createElement('a');
                        elink.download = filename+'_'+this.getdate()+".xls";
                        // console.log(this.getdate())
                        elink.style.display = 'none';
                        elink.href = URL.createObjectURL(blob);
                        document.body.appendChild(elink);
                        elink.click();
                        document.body.removeChild(elink);
                    }
                }).catch(err => {
                    console.warn(err);
                });
         
    },

    //
    getdate() {
          var now = new Date(),
              y = now.getFullYear(),
              m = now.getMonth() + 1,
              d = now.getDate();
          return y + "-" + (m < 10 ? "0" + m : m) + "-" + (d < 10 ? "0" + d : d) + " " + now.toTimeString().substr(0, 8);
      },

    //
      getPayOrderStatusList(){
          let data = {
            payOrderStatus: this.searchform.payOrderStatus
          };
          this.$http
            .post(this.$store.state.domain +"/loanManage/getPayOrderStatus", data)
              .then(
              //
              response => {
                if (response.data.code == 0) { 
                    this.PayOrderStatusList = response.data.detail.result;
                } 
                //
                else {
                  this.$message({
                    message: response.data.msg,
                    type: "error"
                  });
                }
              },
              //
              response => {
                // console.log(response);
              }
            );
      },
    /**
     * 
     */
    screenfull() {
      if (!screenfull.enabled) {
        this.$message({
          message: 'Your browser does not work',
          type: 'warning'
        })
        return false
      }
      screenfull.toggle()
      this.isFullscreen = true
    },
    /**
     * ESC
     */
    checkFull() {
      var isFull = document.fullscreenEnabled || window.fullScreen || document.webkitIsFullScreen || document.msFullscreenEnabled
      // to fix : false || undefined == undefined
      if (isFull === undefined) {
          isFull = false
      }
      return isFull
    },

    //
    togglemenu(){
        this.$store.state.isLeftHiden=!this.$store.state.isLeftHiden;
        console.log(this.$store.state.isLeftHiden)
    },

    //
    selChange(val){
      let data = {
        productCd: this.searchform.productCd
      };
      this.$http
        .post(this.$store.state.domain +"/loanManage/product/query", data)
          .then(
          //
          response => {
            if (response.data.code == 0) { 
                let productlists = response.data.detail.result;
                //
                for (var vals of productlists){
                  if (val == vals.channelCd){
                    this.productlist=vals.list;
                  }
                }
            } 
            //
            else {
              this.$message({
                message: response.data.msg,
                type: "error"
              });
            }
          },
          //
          response => {
            // console.log(response);
          }
        );
    },

    // 
      // let adminhostidcookie=this.$cookies.get('adminhostid');//cookie1
      // let admintcookie=this.$cookies.get('admint');//cookie2
      getchannellist() {
      let data = {
        channelCd: this.searchform.channelCd
      };
      this.$http
        .post(this.$store.state.domain +"/loanManage/channel/query", data)
          .then(
          //
          response => {
            if (response.data.code == 0) { 
                this.channellist = response.data.detail.result;
            } 
            //
            else {
              this.$message({
                message: response.data.msg,
                type: "error"
              });
            }
          },
          //
          response => {
            // console.log(response);
          }
        );
      },

    //
      getproductlist(){
      let data = {
        productCd: this.searchform.productCd
      };
      this.$http
        .post(this.$store.state.domain +"/loanManage/product/query", data)
          .then(
          //
          response => {
            if (response.data.code == 0) { 
                let productlists = response.data.detail.result;
                //
                for (var vals of productlists){
                  for(var x of vals.list){
                    this.productlist.push(x);
                  }
                }
            } 
            //
            else {
              this.$message({
                message: response.data.msg,
                type: "error"
              });
            }
          },
          //
          response => {
            // console.log(response);
          }
        );
      },

      //
      getloantypelist(){
          let data = {
            loanType: this.searchform.loanType
          };
          this.$http
            .post(this.$store.state.domain +"/loanManage/getLoanType", data)
              .then(
              //
              response => {
                if (response.data.code == 0) { 
                    this.loantypelist = response.data.detail.result;
                } 
                //
                else {
                  this.$message({
                    message: response.data.msg,
                    type: "error"
                  });
                }
              },
              //
              response => {
                // console.log(response);
              }
            );
      },

      // excel
      // exportExcel () {
      //     /* generate workbook object from table */
      //     var wb = XLSX.utils.table_to_book(document.querySelector('#app'))
      //     /* get binary string as output */
      //     var wbout = XLSX.write(wb, { bookType: 'xlsx', bookSST: true, type: 'array' })
      //     try {
      //         FileSaver.saveAs(new Blob([wbout], { type: 'application/octet-stream' }), '.xlsx')
      //     } catch (e) { if (typeof console !== 'undefined') console.log(e, wbout) }
      //     return wbout
      // },

      //vue  sessionStorage
      getName() {
        let userName = sessionStorage.getItem("name");
        this.userName = userName;
      },
      // 
      goBack() {
        var _this = this;
				this.$confirm('?', '', {
					//type: 'warning'
				}).then(() => {
          window.sessionStorage.removeItem('name');//sessionname
					this.$router.push('/login');
				}).catch(() => {

				});
      },
      // 
      search() {
        this.getRate();
        this.getTerm();
        this.getloan();
        this.getlist();

      },
      // 
      resetForm(formName) {
        this.$refs[formName].resetFields();
        this.productlist="";
        this.searchform.beginDate="";
        this.searchform.endDate="";
        this.searchform.payOrderStatus="";
        this.getRate();
        this.getTerm();
        this.getloan();
        // this.getlist();
      },
      // pagesize
      handleSizeChange(psize) {
        this.searchform.pageSize = psize;
        // 
        this.searchform.pageIndex =1;
        this.getlist();
      },
      // currentPage
      handleCurrentChange(pindex) {
        this.searchform.pageIndex = pindex;
        this.getlist();
      },

      // 
      gouserdetail(processNo,channelCode) {
        //
        if(channelCode=="WHZHWS"){
            this.$router.push("/users/detailwanshang?processNo=" + processNo);
        }
        else if(channelCode=="SDLY"){
            this.$router.push("/users/detaillinyi?processNo=" + processNo);
        }else if(channelCode=="BJHY"){
            this.$router.push("/users/detailhuayu?processNo=" + processNo);
        }else if(channelCode=="BJZH"){
            this.$router.push("/users/detailzhonghe?processNo=" + processNo);
        }else if(channelCode=="M008"){
            this.$router.push("/users/detailhuayu?processNo=" + processNo);
        }else if(channelCode=="M012"){
            this.$router.push("/users/detailshenshengrong?processNo=" + processNo);
        }else if(channelCode=="M015"){
            this.$router.push("/users/detailguhe?processNo=" + processNo);
        }else if(channelCode=="M016"){
            this.$router.push("/users/detailqianhai?processNo=" + processNo);
        }else if(channelCode=="M017"){
            this.$router.push("/users/detailyunkedai?processNo=" + processNo);
        }
        else if(channelCode=="M019"){
            this.$router.push("/users/detailxiaoye?processNo=" + processNo);
        }else if(channelCode=="M018" || channelCode=="M023" || channelCode=="M024"){
            this.$router.push("/users/detailbaiduo?processNo=" + processNo);
        }
        else if(channelCode=="M009"){
            this.$router.push("/users/detailjiarongbao?processNo=" + processNo);
        }else if(channelCode=="M020"){
            this.$router.push("/users/detailchangsheng?processNo=" + processNo);
        }else if(channelCode=="M021"){
            this.$router.push("/users/detailweirong?processNo=" + processNo);
        }else if(channelCode=="M022"){
            this.$router.push("/users/detailhairong?processNo=" + processNo);
        }else{
            this.$router.push("/users/detaillinyi?processNo=" + processNo);
        }
      },

      // ajaxVue  this.$http  HTTP 
      getlist() {
        this.listLoading=true;
        this.$http
          .post(
            this.$store.state.domain +"/loanManage/loanPay", this.searchform 
          )
          //then()then()then()
          .then(  
            // Lambda
            response => {
              if (response.data.code == 0) {
                //
                this.tableData = response.data.detail.result.pageList;
                this.pageSize= response.data.detail.result.pageSize;
                this.pageIndex=response.data.detail.result.pageIndex;
                this.count= response.data.detail.result.count;
                if(response.data.detail.result.params==null){
                  this.allmoney=0;
                }
                else{
                  this.allmoney = response.data.detail.result.params.payAmtTotal;
                }
                this.listLoading=false;

                if(this.tableData==null)
                {
                  this.$notify({
                    message: '',
                    type: 'warning',
                    duration:"2000"//
                  });
                }
              } else {
              //   this.listLoading=true;
              // //
              //   this.$message({
              //       message: response.data.msg,
              //       type: "error"
              //     });
              }
              
            },
            response => {
              //
                this.listLoading=false;
                this.$message({
                    dangerouslyUseHTMLString: true,//html
                    message: '<svg class="icon" aria-hidden="true"> <use xlink:href="#icon-shengqi"></use> </svg> '+
                    response.body.message,
                    type: "error"
                  });
              console.log(response);
            }
          );
      },

      //
    getRate(){
      if(this.searchform.intRate=="A"){
          this.searchform.intRateBegin=0;
          this.searchform.intRateEnd=0.0083;
        }
        else if(this.searchform.intRate=="B"){
          this.searchform.intRateBegin=0.0083;
          this.searchform.intRateEnd=0.0125;
        }
        else if(this.searchform.intRate=="C"){
          this.searchform.intRateBegin=0.0125;
          this.searchform.intRateEnd=0.0167;
        }
        else if(this.searchform.intRate=="D"){
          this.searchform.intRateBegin=0.0167;
          this.searchform.intRateEnd=0.0208;
        }
        else if(this.searchform.intRate=="E"){
          this.searchform.intRateBegin=0.0208;
          this.searchform.intRateEnd="";
        }
        else{
          this.searchform.intRateBegin="";
          this.searchform.intRateEnd="";
        }
        return;

    },
    getTerm(){
      if(this.searchform.term=="A"){
          this.searchform.termBegin=0;
          this.searchform.termEnd=3;
        }
        else if(this.searchform.term=="B"){
          this.searchform.termBegin=3;
          this.searchform.termEnd=6;
        }
        else if(this.searchform.term=="C"){
          this.searchform.termBegin=6;
          this.searchform.termEnd=12;
        }
        else if(this.searchform.term=="D"){
          this.searchform.termBegin=12;
          this.searchform.termEnd="";
        }
        else{
          this.searchform.termBegin="";
          this.searchform.termEnd="";
        }
    },
    getloan(){
        if(this.searchform.payAmt=="A"){
          this.searchform.payAmtBegin=0;
          this.searchform.payAmtEnd=50000;
        }
        else if(this.searchform.payAmt=="B"){
          this.searchform.payAmtBegin=50000;
          this.searchform.payAmtEnd=100000;
        }
        else if(this.searchform.payAmt=="C"){
          this.searchform.payAmtBegin=100000;
          this.searchform.payAmtEnd=500000;
        }
        else if(this.searchform.payAmt=="D"){
          this.searchform.payAmtBegin=500000;
          this.searchform.payAmtEnd=1000000;
        }
        else if(this.searchform.payAmt=="E"){
          this.searchform.payAmtBegin=1000000;
          this.searchform.payAmtEnd="";
        }
        else{
          this.searchform.payAmtBegin="";
          this.searchform.payAmtEnd="";
        }
    },
  },

    //
    filters: {
          formatDate(time) {
              var date = new Date(time);
              return formatDate(date, 'yyyy-MM-dd hh:mm');
          }
    },
    
  
};
