<template>
  <!-- truefalse -->
  <div v-if="userName==this.$store.state.nameler?false:true">
    <div class="topBanner">
      <el-tooltip class="item" effect="light" content="" placement="bottom">
        <p class="tog" @click="togglemenu">
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#icon-zhankai1" />
          </svg>
        </p>
      </el-tooltip>
      <p class="left"> >> </p>
      <p class="right" @click="goBack">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-shouye" />
        </svg>
      </p>

      <p class="right" title="">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-character" />
        </svg>
        {{userName}}
      </p>

      <p class="right" @click="screenfull">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-quanping4" />
        </svg>
      </p>

      <p class="right" title="">
        <router-link to="/yonghuguanli/mimaxiugai" class="roulink">
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#icon-mimazhongzhi" />
          </svg>
        </router-link>
      </p>

      <p class="right" @click="lastpage">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-fanhui2" />
        </svg>
      </p>
    </div>

    <div class="content">
      <div class="searcharea">
        <!-- el-formmodelel-form-itemel-form-itemprop -->
        <el-form ref="searchform" :model="searchform" label-width="80px" size="mini">
          <el-row :gutter="30">
            <el-col :span="6">
              <el-form-item label="" prop="name">
                <el-input v-model="searchform.name" clearable></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="" prop="phone">
                <el-input v-model="searchform.phone" clearable></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="" prop="idcard">
                <el-input v-model="searchform.idcard" clearable></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="6">
              <div class="di">
                <div class="wrapper">
                  <el-button type="primary" plain @click="submitForm()" size="mini"></el-button>
                  <el-button type="primary" plain @click="resetForm('searchform')" size="mini"></el-button>
                </div>
              </div>
            </el-col>
          </el-row>
        </el-form>
      </div>
      <!-- ===============================  =================================== -->
      <el-table
        :data="tableData"
        border
        size="medium"
        stripe
        v-loading="listLoading"
        element-loading-text=""
        element-loading-spinner="el-icon-loading"
        element-loading-background="rgba(0, 0, 0, 0.8)"
        style="width: 100%; height:100%;"
      >
        <el-table-column prop="name" label="" align="center"></el-table-column>
        <el-table-column prop="phone" label="" align="center"></el-table-column>
        <el-table-column prop="idcard" label="" align="center"></el-table-column>
        <el-table-column prop="bankid" label="" align="center"></el-table-column>
        <el-table-column prop="idcardFront" label="" align="center">
          <template slot-scope="scope">
            <template v-if="scope.row.idcardFront">
              <a :href="scope.row.idcardFront" target="_blank"></a>
            </template>
          </template>
        </el-table-column>
        <el-table-column prop="idcardBack" label="" align="center">
          <template slot-scope="scope">
            <template v-if="scope.row.idcardBack">
              <a :href="scope.row.idcardBack" target="_blank"></a>
            </template>
          </template>
        </el-table-column>
        <el-table-column prop="pledgeContractImg" label="" align="center">
          <template slot-scope="scope">
            <template v-if="scope.row.pledgeContractImg">
              <a :href="scope.row.pledgeContractImg" target="_blank"></a>
            </template>
          </template>
        </el-table-column>
      </el-table>
      <div class="human-pagination">
        <el-pagination
          background
          style="text-align:center"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="this.searchform.pageIndex"
          :page-sizes="[20,50,100]"
          :page-size="this.searchform.pageSize"
          layout="total, sizes, prev, pager, next"
          :total="count"
        >
          <!---->
        </el-pagination>
      </div>
    </div>
  </div>
</template>
<script>
// 
import { formatDate } from "../../common/date.js";
import screenfull from "screenfull";
//vuex
// import {mapState} from 'vuex'

export default {
  data() {
    return {
      isFullscreen: false,
      userName: "",
      listLoading: false, //
      //
      tableData: [],
      count:0,
      searchform: {
        name: "", //
        phone: "", //
        idcard: "", //
        pageIndex: 1, //
        pageSize: 50 //
      }
    };
  },
  // mounted,html
  mounted() {
    this.getName(); //
    window.onresize = () => {
      // ESC
      if (!this.checkFull()) {
        // esc
        this.isFullscreen = false;
      }
    };
    var data = {};
    this.load(data);
  },

  //html
  created() {
    // this.getlist();//
  },
  methods: {
    //
    lastpage() {
      window.history.go(-1);
    },
    /**
     * 
     */
    screenfull() {
      if (!screenfull.enabled) {
        this.$message({
          message: "Your browser does not work",
          type: "warning"
        });
        return false;
      }
      screenfull.toggle();
      this.isFullscreen = true;
    },
    /**
     * ESC
     */
    checkFull() {
      var isFull =
        document.fullscreenEnabled ||
        window.fullScreen ||
        document.webkitIsFullScreen ||
        document.msFullscreenEnabled;
      // to fix : false || undefined == undefined
      if (isFull === undefined) {
        isFull = false;
      }
      return isFull;
    },

    //
    togglemenu() {
      this.$store.state.isLeftHiden = !this.$store.state.isLeftHiden;
      console.log(this.$store.state.isLeftHiden);
    },

    //vue  sessionStorage
    getName() {
      let userName = sessionStorage.getItem("name");
      this.userName = userName;
    },

    // 
    goBack() {
      var _this = this;
      this.$confirm("?", "", {
        //type: 'warning'
      })
        .then(() => {
          window.sessionStorage.removeItem("name"); //sessionname
          this.$router.push("/login");
        })
        .catch(() => {});
    },

    submitForm() {
      this.load(this.searchform);
    },
    // 
    resetForm(formName) {
      this.$refs[formName].resetFields();
      // this.getlist();
    },
    handleSizeChange(psize) {
      // 
      this.searchform.pageSize = psize;
      // 
      this.searchform.pageIndex = 1;
      this.load(this.searchform);
    },

    // currentPage
    handleCurrentChange(pindex) {
      this.searchform.pageIndex = pindex;
      this.load(this.searchform);
    },
    //
    load(data) {
      this.$axios({
        method: "post",
        url: this.$store.state.domain + "/sld/user/userlist",
        data: data
      }).then(
        response => {
          var res = response.data;
          if (res.code == 0) {
            this.tableData = res.detail.result.pageList;
            this.count = res.detail.result.count;
            this.searchform.pageIndex = res.detail.result.pageIndex;
            this.searchform.pageSize = res.detail.result.pageSize;
          } else {
            this.$message({
              message: res.msg,
              type: "error"
            });
          }
        },
        error => {}
      );
    }
  },

  //
  filters: {
    formatDate(time) {
      var date = new Date(time);
      return formatDate(date, "yyyy-MM-dd hh:mm");
    }
  }
};
</script>

 <style lang='less'>
//  less
@import "../users/index.css";
.human-pagination {
    margin-top: 30px;
  }
</style>



