<script>
//vuex
import { mapState } from "vuex";

export default {
  data() {
    return {
      isCollapse: true,
      showsystem: false //
    };
  },

  // computed:mapState({
  //     roleId:function(state){
  //       if(state.roleId == ''){
  //         this.$store.commit('getroleId')
  //       }
  //       return state.roleId;
  //     },
  //     roleName:function(state){
  //       if(state.roleName == ''){
  //         this.$store.commit('getroleName')
  //       }
  //       return state.roleName;
  //     }
  //   }),

  mounted() {
    this.getrole();
  },
  methods: {
    //vue  sessionStorage
    getrole() {
      var rolename = sessionStorage.getItem("role");
      console.log(sessionStorage.getItem("role"));
      if (rolename == "showtrue") {
        this.showsystem = true;
      } else {
        this.showsystem = false;
      }
    },

    logout() {
      let _this = this;
      _this.$http
        .post("/api/v1.2/video_class/manage?action=logout")
        .then(response => {
          _this.$router.push("/login");
        });
    }
  }
};
</script>

