<template>
  <div v-if="userName==this.$store.state.nameler?false:true">
    <div class="topBanner">
      <el-tooltip class="item" effect="light" content="" placement="bottom">
        <p class="tog" @click="togglemenu">
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#icon-zhankai1"></use>
          </svg>
        </p>
      </el-tooltip>

      <p class="left"> >> </p>

      <p class="right" @click="goBack">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-shouye"></use>
        </svg>
      </p>

      <p class="right">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-character"></use>
        </svg>
        {{userName}}
      </p>

      <p class="right" @click="screenfull">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-quanping4"></use>
        </svg>
      </p>

      <p class="right" title="">
        <router-link to="/yonghuguanli/mimaxiugai" class="roulink">
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#icon-mimazhongzhi"></use>
          </svg>
        </router-link>
      </p>

      <p class="right" @click="lastpage">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-fanhui2"></use>
        </svg>
      </p>
    </div>

    <div class="content">
      <div class="searcharea">
        <el-form ref="searchform" :model="searchform" label-width="100%" size="mini">
          <el-row :gutter="30">
            <el-col :span="6">
              <div class="grid-content bg-purple">
                <el-form-item label="" prop="name">
                  <el-input v-model="searchform.name" clearable></el-input>
                </el-form-item>
              </div>
            </el-col>

            <el-col :span="6">
              <div class="grid-content bg-purple">
                <el-form-item label="" prop="channelCd">
                  <el-select
                    v-model="searchform.channelCd"
                    placeholder=""
                    clearable
                    @change="selChange($event)"
                  >
                    <!--  -->
                    <el-option
                      v-for="item in channellist"
                      :label="item.channelCode"
                      :key="item.channelCode"
                      :value="item.channelCode"
                    ></el-option>
                  </el-select>
                </el-form-item>
              </div>
            </el-col>

            <el-col :span="6">
              <div class="grid-content bg-purple">
                <el-form-item label="" prop="productCd">
                  <el-select v-model="searchform.productCd" placeholder="" clearable>
                    <!--  -->
                    <el-option
                      v-for="item in productlist"
                      :label="item.productCode"
                      :key="item.productCode"
                      :value="item.productCode"
                    ></el-option>
                  </el-select>
                </el-form-item>
              </div>
            </el-col>

            <!-- <el-col :span="6">
                <div class="grid-content bg-purple">
                  <el-form-item label="" prop="repayType">
            <el-select v-model="searchform.repayType" placeholder="">-->
            <!--  -->
            <!-- <el-option v-for="item in payChannellist"
                              :label="item.desc"
                              :key="item.type"
                              :value="item.type"
                          ></el-option>
                  </el-select>
                  </el-form-item>
                </div>
            </el-col>-->
          </el-row>

          <el-row :gutter="30">
            <el-col :span="6">
              <el-form-item label="" prop="repayStatus">
                <el-select v-model="searchform.repayStatus" placeholder="" clearable>
                  <!--  -->
                  <el-option
                    v-for="item in RepayStatusList"
                    :label="item.description"
                    :key="item.code"
                    :value="item.code"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>

            <el-col :span="6">
              <el-form-item label="" prop="beginDate">
                <el-date-picker
                  class="shi"
                  clearable
                  v-model="searchform.beginDate"
                  value-format="yyyy-MM-dd HH:mm:ss"
                  type="datetime"
                  placeholder=""
                  default-time="00:00:00"
                ></el-date-picker>
              </el-form-item>
            </el-col>

            <el-col :span="6">
              <el-form-item label="" prop="endDate">
                <el-date-picker
                  class="shi"
                  clearable
                  v-model="searchform.endDate"
                  value-format="yyyy-MM-dd HH:mm:ss"
                  type="datetime"
                  placeholder=""
                  default-time="23:59:59"
                ></el-date-picker>
              </el-form-item>
            </el-col>

            <el-col :span="6">
              <div class="di">
                <span class="wrapper">
                  <!--  -->
                  <el-button type="primary" plain @click="search()" size="mini"></el-button>
                  <el-button type="primary" plain @click="resetForm('searchform')" size="mini"></el-button>
                </span>
              </div>
            </el-col>
          </el-row>
        </el-form>
      </div>

      <div class="cen-box">
        <div class="dl-center">
          <el-col :span="6">
            <el-tag type="danger" size="mini">{{allmoney}}</el-tag>
          </el-col>
        </div>

        <div class="dr-center">
          <a id="downlink"></a>
          <el-col :span="6">
            <el-button type="success" plain @click="exportExcel('')" size="mini">Excel</el-button>
          </el-col>
        </div>
      </div>

      <!--  -->
      <el-table
        :data="tableData"
        v-loading="listLoading"
        element-loading-text=""
        element-loading-spinner="el-icon-loading"
        element-loading-background="rgba(0, 0, 0, 0.8)"
        size="medium"
        stripe
        border
        style="width: 100%;height:100%;"
      >
        <el-table-column prop="processNo" label="" align="center"></el-table-column>
        <el-table-column prop="contractNo" label="" width=210 align="center"></el-table-column>
        <el-table-column prop="name" label="" align="center">
          <!--  -->
          <template slot-scope="scope">
            <el-button
              type="text"
              size="small"
              @click="gouserdetail(scope.row.processNo,scope.row.channelCode)"
            >{{scope.row.name}}</el-button>
          </template>
        </el-table-column>
        <el-table-column v-if="show" prop="processNo" label="" align="center"></el-table-column>

        <!-- <el-table-column prop="channelCode" label="" align="center">
        </el-table-column>-->
        <el-table-column prop="productCode" label="" align="center"></el-table-column>
        <el-table-column prop="actualPrin" label="" align="center"></el-table-column>
        <el-table-column prop="actualInt" label="" align="center"></el-table-column>
        <el-table-column prop="repayAmt" label="" align="center"></el-table-column>
        <!-- <el-table-column prop="repayType" label="" align="center">
        </el-table-column>-->
        <!-- <el-table-column prop="statementType" label="" align="center">
        </el-table-column>-->
        <el-table-column prop="repayStatus" label="" align="center"></el-table-column>
        <el-table-column prop="repayTime" label="" align="center"></el-table-column>
      </el-table>
      <div class="block">
        <el-pagination
          background
          style="text-align:center"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="this.searchform.pageIndex"
          :page-sizes="[20,50,100]"
          :page-size="this.searchform.pageSize"
          layout="total, sizes, prev, pager, next"
          :total="count"
        ></el-pagination>
      </div>
    </div>
  </div>
</template>
<script>
import { formatDate } from "../../common/date.js";
import screenfull from "screenfull";

// xlsx
import FileSaver from "file-saver";
import XLSX from "xlsx";

//vuex
import { mapState } from "vuex";

export default {
  data() {
    return {
      isFullscreen: false,
      show: false, //
      userName: "",
      listLoading: false,
      status: "none",
      allmoney: 0,

      //
      channellist: [],

      //
      productlist: [],

      //
      payChannellist: [],

      //
      RepayStatusList: [],

      count: 0,
      pageIndex: 1, //
      pageSize: 50, //
      tableData: [], //
      searchform: {
        name: "", //
        channelCd: "", //
        productCd: "", //
        repayType: "", //
        repayStatus: "", //
        beginDate: new Date().toLocaleDateString() + " 00:00:00", //
        endDate: new Date().toLocaleDateString() + " 23:59:59", //
        pageIndex: 1,
        pageSize: 50
      }
    };
  },
  mounted() {
    this.getName();

    this.getchannellist(); //

    // this.getproductlist();//

    this.getpayChannellist(); //

    this.getRepayStatusList(); //

    window.onresize = () => {
      // ESC
      if (!this.checkFull()) {
        // esc
        this.isFullscreen = false;
      }
    };
  },
  //html
  created() {
    this.getlist();
  },
  methods: {
    //
    lastpage() {
      window.history.go(-1);
    },
    // excel
    exportExcel(filename) {
      const url = this.$store.state.domain + "/loanManage/loanRepayExport";
      this.$http
        .post(url, this.searchform, {
          responseType: "blob"
        })
        .then(res => {
          let blob = new Blob([res.data], {
            type:
              "application/vnd.ms-excel,application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
          });
          if (window.navigator.msSaveOrOpenBlob) {
            navigator.msSaveBlob(blob);
          } else {
            let elink = document.createElement("a");
            elink.download = filename + "_" + this.getdate() + ".xls";
            console.log(this.getdate());
            elink.style.display = "none";
            elink.href = URL.createObjectURL(blob);
            document.body.appendChild(elink);
            elink.click();
            document.body.removeChild(elink);
          }
        })
        .catch(err => {
          console.warn(err);
        });
    },

    //
    getdate() {
      var now = new Date(),
        y = now.getFullYear(),
        m = now.getMonth() + 1,
        d = now.getDate();
      return (
        y +
        "-" +
        (m < 10 ? "0" + m : m) +
        "-" +
        (d < 10 ? "0" + d : d) +
        " " +
        now.toTimeString().substr(0, 8)
      );
    },

    // 
    getRepayStatusList() {
      let data = {
        RepayStatus: this.searchform.RepayStatus
      };
      this.$http
        .post(this.$store.state.domain + "/loanManage/getRepayStatus", data)
        .then(
          //
          response => {
            if (response.data.code == 0) {
              this.RepayStatusList = response.data.detail.result;
            }
            //
            else {
              this.$message({
                message: response.data.msg,
                type: "error"
              });
            }
          },
          //
          response => {
            // console.log(response);
          }
        );
    },
    /**
     * 
     */
    screenfull() {
      if (!screenfull.enabled) {
        this.$message({
          message: "Your browser does not work",
          type: "warning"
        });
        return false;
      }
      screenfull.toggle();
      this.isFullscreen = true;
    },
    /**
     * ESC
     */
    checkFull() {
      var isFull =
        document.fullscreenEnabled ||
        window.fullScreen ||
        document.webkitIsFullScreen ||
        document.msFullscreenEnabled;
      // to fix : false || undefined == undefined
      if (isFull === undefined) {
        isFull = false;
      }
      return isFull;
    },

    //
    togglemenu() {
      this.$store.state.isLeftHiden = !this.$store.state.isLeftHiden;
      console.log(this.$store.state.isLeftHiden);
    },

    //
    selChange(val) {
      let data = {
        productCd: this.searchform.productCd
      };
      this.$http
        .post(this.$store.state.domain + "/loanManage/product/query", data)
        .then(
          //
          response => {
            if (response.data.code == 0) {
              let productlists = response.data.detail.result;
              //
              for (var vals of productlists) {
                if (val == vals.channelCd) {
                  this.productlist = vals.list;
                }
              }
            }
            //
            else {
              this.$message({
                message: response.data.msg,
                type: "error"
              });
            }
          },
          //
          response => {
            // console.log(response);
          }
        );
    },

    // 
    getchannellist() {
      let data = {
        channelCd: this.searchform.channelCd
      };
      this.$http
        .post(this.$store.state.domain + "/loanManage/channel/query", data)
        .then(
          //
          response => {
            if (response.data.code == 0) {
              this.channellist = response.data.detail.result;
            }
            //
            else {
              this.$message({
                message: response.data.msg,
                type: "error"
              });
            }
          },
          //
          response => {
            // console.log(response);
          }
        );
    },

    //
    getproductlist() {
      let data = {
        productCd: this.searchform.productCd
      };
      this.$http
        .post(this.$store.state.domain + "/loanManage/product/query", data)
        .then(
          //
          response => {
            if (response.data.code == 0) {
              let productlists = response.data.detail.result;
              //
              for (var vals of productlists) {
                for (var x of vals.list) {
                  this.productlist.push(x);
                }
              }
            }
            //
            else {
              this.$message({
                message: response.data.msg,
                type: "error"
              });
            }
          },
          //
          response => {
            // console.log(response);
          }
        );
    },

    //
    getpayChannellist() {
      let data = {
        payChannel: this.searchform.payChannel
      };
      this.$http
        .post(this.$store.state.domain + "/loanManage/getRepayType", data)
        .then(
          //
          response => {
            if (response.data.code == 0) {
              this.payChannellist = response.data.detail.result;
            }
            //
            else {
              this.$message({
                message: response.data.msg,
                type: "error"
              });
            }
          },
          //
          response => {
            // console.log(response);
          }
        );
    },

    //excel:
    // exportExcel () {
    //     /* generate workbook object from table */
    //     var wb = XLSX.utils.table_to_book(document.querySelector('#app'))
    //     /* get binary string as output */
    //     var wbout = XLSX.write(wb, { bookType: 'xlsx', bookSST: true, type: 'array' })
    //     try {
    //         FileSaver.saveAs(new Blob([wbout], { type: 'application/octet-stream' }), '.xlsx')
    //     } catch (e) { if (typeof console !== 'undefined') console.log(e, wbout) }
    //     return wbout
    // },

    getName() {
      let userName = sessionStorage.getItem("name");
      this.userName = userName;
    },

    goBack() {
      var _this = this;
      this.$confirm("?", "", {
        //type: 'warning'
      })
        .then(() => {
          sessionStorage.removeItem("name");
          this.$router.push("/login");
        })
        .catch(() => {});
    },

    search() {
      this.getlist();
    },

    resetForm(formName) {
      this.$refs[formName].resetFields();
      this.productlist = "";
      this.searchform.beginDate = "";
      this.searchform.endDate = "";
      // this.getlist();
    },

    handleSizeChange(psize) {
      this.searchform.pageSize = psize;
      // 
      this.searchform.pageIndex = 1;
      this.getlist();
    },

    handleCurrentChange(pindex) {
      this.searchform.pageIndex = pindex;
      this.getlist();
    },

    // 
    gouserdetail(processNo, channelCode) {
      //
      if (channelCode == "WHZHWS") {
        this.$router.push("/users/detailwanshang?processNo=" + processNo);
      } else if (channelCode == "SDLY") {
        this.$router.push("/users/detaillinyi?processNo=" + processNo);
      } else if (channelCode == "BJHY") {
        this.$router.push("/users/detailhuayu?processNo=" + processNo);
      } else if (channelCode == "BJZH") {
        this.$router.push("/users/detailzhonghe?processNo=" + processNo);
      } else if (channelCode == "M008") {
        this.$router.push("/users/detailhuayu?processNo=" + processNo);
      } else if (channelCode == "M012") {
        this.$router.push("/users/detailshenshengrong?processNo=" + processNo);
      } else if (channelCode == "M015") {
        this.$router.push("/users/detailguhe?processNo=" + processNo);
      } else if (channelCode == "M016") {
        this.$router.push("/users/detailqianhai?processNo=" + processNo);
      } else if (channelCode == "M017") {
        this.$router.push("/users/detailyunkedai?processNo=" + processNo);
      } else if (channelCode == "M019") {
        this.$router.push("/users/detailxiaoye?processNo=" + processNo);
      } else if (
        channelCode == "M018" ||
        channelCode == "M023" ||
        channelCode == "M024"
      ) {
        this.$router.push("/users/detailbaiduo?processNo=" + processNo);
      } else if (channelCode == "M009") {
        this.$router.push("/users/detailjiarongbao?processNo=" + processNo);
      } else if (channelCode == "M020") {
        this.$router.push("/users/detailchangsheng?processNo=" + processNo);
      } else if (channelCode == "M021") {
        this.$router.push("/users/detailweirong?processNo=" + processNo);
      } else if (channelCode == "M022") {
        this.$router.push("/users/detailhairong?processNo=" + processNo);
      } else {
        this.$router.push("/users/detaillinyi?processNo=" + processNo);
      }
    },

    getlist() {
      this.listLoading = true;
      this.$http
        .post(
          this.$store.state.domain + "/loanManage/loanRepay",
          this.searchform
        )
        .then(
          response => {
            if (response.data.code == 0) {
              //
              this.tableData = response.data.detail.result.pageList;
              this.count = response.data.detail.result.count;
              this.pageSize = response.data.detail.result.pageSize;
              this.pageIndex = response.data.detail.result.pageIndex;
              if (response.data.detail.result.params == null) {
                this.allmoney = 0;
              } else {
                this.allmoney =
                  response.data.detail.result.params.repayAmtTotal;
              }
              this.listLoading = false;

              if (this.tableData == null) {
                this.$notify({
                  message: "",
                  type: "warning",
                  duration: "2000" //
                });
              }
            } else {
              // this.listLoading=true;
              // this.$message.error(response.data.msg);
            }
          },
          response => {
            //
            this.listLoading = false;
            this.$message({
              dangerouslyUseHTMLString: true, //html
              message:
                '<svg class="icon" aria-hidden="true"> <use xlink:href="#icon-shengqi"></use> </svg> ' +
                response.body.message,
              type: "error"
            });
            console.log(response);
          }
        );
    }
  },
  filters: {
    formatDate(time) {
      var date = new Date(time);
      return formatDate(date, "yyyy-MM-dd hh:mm");
    }
  },
  watch: {},
  components: {}
};
</script>

 <style lang='less'>
@import "../users/index.css";
</style>



