<template>

<!-- truefalse -->
  <div v-if="userName==this.$store.state.nameler?false:true">
    

    <div class="topBanner">
      
      <el-tooltip class="item" effect="light" content="" placement="bottom">
      <p class='tog' @click="togglemenu">
        <svg class="icon" aria-hidden="true">
          <use xlink:href= "#icon-zhankai1"></use>
        </svg></p>
      </el-tooltip>

      <p class='left'> >> </p>

      <p class='right' @click="goBack" >
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-shouye"></use>
        </svg></p>

      <p class='right' title="">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-character"></use>
        </svg>{{userName}}</p>
      
      <p class='right' @click="screenfull">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-quanping4"></use>
      </svg></p>

      <p class='right' title="">
      <router-link to="/yonghuguanli/mimaxiugai" class="roulink">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-mimazhongzhi"></use>
      </svg>
      </router-link>
      </p>

      <p class='right' @click="lastpage">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-fanhui2"></use>
      </svg></p>
    </div>
  
<div class="content">
        <div class="searcharea">
          <!-- el-formmodelel-form-itemel-form-itemprop -->
          <el-form ref="searchform" :model="searchform" label-width="80px" size="mini">

            <el-row :gutter="30">
              <el-col :span="8">
                  <el-form-item label="" prop="channelCd" >
                    <el-select v-model="searchform.channelCd" class="sh"  multiple placeholder="" collapse-tags @change="selchange">
                    <el-option v-for="item in channellist"
                        :key="item.channelCode"
                        :label="item.channelCode"
                        :value="item.channelCode"></el-option>
                  </el-select>
                  </el-form-item>
              </el-col>

              <el-col :span="6">
                <div class="di">
                  <div class="wrapper">
                    <el-button type="primary" plain @click="search()" size="mini">
                      </el-button>
                    <el-button type="primary" plain @click="resetForm('searchform')" size="mini">
                      
                    </el-button>
                  </div>
                </div>
              </el-col>
            </el-row>
          </el-form>
        </div>

        <!-- ===============================  =================================== -->
       
          <el-table :data="tableData"  border size="medium"
          stripe v-loading="listLoading"  border
          element-loading-text=""
          element-loading-spinner="el-icon-loading"
          element-loading-background="rgba(0, 0, 0, 0.8)"
          style="width: 100%; height:100%;">
            <el-table-column prop="channelCd" label="" align="center">
            </el-table-column>
            <el-table-column prop="productCd" label="" width="280px" align="center">
            </el-table-column>
            <el-table-column prop="merchant_id" label="" align="center">
            </el-table-column>
            <el-table-column prop="pay_name" label="" width="410px" align="center">
            </el-table-column>
            <el-table-column prop="valid_Amount" label="" align="center">
              <template slot-scope="scope">
                <!--  -->
                {{parseFloat(scope.row.valid_Amount).toFixed(2).replace(/(\d)(?=(\d{3})+\.)/g, '$&,')}}
              </template>
            </el-table-column>
          </el-table>
</div>
  </div>
</template>
<script>
// 
import {formatDate} from '../../common/date.js';
import screenfull from 'screenfull'
//vuex
// import {mapState} from 'vuex'

export default {
  data() {
    return {
      isFullscreen: false,
      userName: "",
      statuss: "none",
      oldOptions: [],
      listLoading: false,//

      //
      channellist:[],

      //
      tableData: [],

      searchform: {
        channelCd: '',//
        productCd:'',
      }
    };
  },
  // mounted,html
  mounted() {
    this.getName();//
    
    this.getchannellist();//

    window.onresize = () => {
            // ESC
            if (!this.checkFull()) {
              // esc
              this.isFullscreen = false
            }
          }

    // this.getlist();//
    
  },

  methods: {
    //
    selchange(val){
      let allValues = []
        //
        for (let item of this.channellist) {
            allValues.push(item.channelCode)
        }

        // 
        const oldVal = this.oldOptions.length === 0 ? [] : this.oldOptions[1]

        // 
        if (val.includes('')) this.searchform.channelCd = allValues 

        //     
        if (oldVal.includes('') && !val.includes('')) this.searchform.channelCd = []

        //      
        //  
        if (oldVal.includes('') && val.includes('')) {
            const index = val.indexOf('')
            val.splice(index, 1) // 
            this.searchform.channelCd = val
        }

        //    
        if (!oldVal.includes('') && !val.includes('')) {
            if (val.length === allValues.length - 1) this.searchform.channelCd = [''].concat(val)
        }

        //  
        this.oldOptions[1] = this.searchform.channelCd
    },
    
    //vue  sessionStorage
      getName() {
        let userName = sessionStorage.getItem("name");
        this.userName = userName;
      },
      
      // 
      goBack() {
        var _this = this;
				this.$confirm('?', '', {
					//type: 'warning'
				}).then(() => {
          window.sessionStorage.removeItem('name');//sessionname
					this.$router.push('/login');
				}).catch(() => {
			});
    },

    //
    lastpage(){
      window.history.go(-1);
    },
    /**
     * 
     */
    screenfull() {
      if (!screenfull.enabled) {
        this.$message({
          message: 'Your browser does not work',
          type: 'warning'
        })
        return false
      }
      screenfull.toggle()
      this.isFullscreen = true
    },
    /**
     * ESC
     */
    checkFull() {
      var isFull = document.fullscreenEnabled || window.fullScreen || document.webkitIsFullScreen || document.msFullscreenEnabled
      // to fix : false || undefined == undefined
      if (isFull === undefined) {
          isFull = false
      }
      return isFull
    },


    //
    togglemenu(){
        this.$store.state.isLeftHiden=!this.$store.state.isLeftHiden;
        console.log(this.$store.state.isLeftHiden)
    },

    // 
      getchannellist() {
      let data = {
        channelCd: this.searchform.channelCd
      };
      this.$http
        .post(this.$store.state.domain +"/loanManage/channel/query", data)
          .then(
          //
          response => {
            if (response.data.code == 0) { 
                //
                this.channellist.push({channelCode: ''});

                for(var i=0;i<(response.data.detail.result).length;i++){

                  this.channellist.push((response.data.detail.result)[i]);
                }
            } 
            //
            else {
              this.$message({
                message: response.data.msg,
                type: "error"
              });
            }
          },
          //
          response => {
            // console.log(response);
          }
        );
      },
    
      // 
      search() {
        this.getlist();
      },

      // 
      resetForm(formName) {
        this.$refs[formName].resetFields();
        // this.getlist();
      },

      // pagesize
      handleSizeChange(psize) {
        // 
        this.searchform.pageSize = psize;
        // 
        this.searchform.pageIndex =1;
        this.getlist();
      },

      // currentPage
      handleCurrentChange(pindex) {
        this.searchform.pageIndex = pindex;
        this.getlist();
      },

      // ajaxVue  this.$http  HTTP 
      getlist() {

        //
      const index = this.searchform.channelCd.indexOf('')

      //
      if(index!=-1){
        this.searchform.channelCd.splice(index, 1) // 
      }

        //
        var str=this.searchform.channelCd
        var spr=str.toString().replace(/\[|]/g,'');
        console.log(spr)
        let data={
          channelCd:spr,
          productCd:""
        }

        this.listLoading=true
        this.$http
          .post(
            this.$store.state.domain +"/MonitorFile/monitorList",data
          )
          //then()then()then()
          .then(  
            // Lambda
            response => {
              if (response.data.code == 0) {
                //
                this.tableData = response.data.detail.result;
                // console.log(this.tableData)
                this.listLoading=false;
                if(this.tableData==null || this.tableData=="")
                {
                  this.$notify({
                    message: '',
                    type: 'warning',
                    duration:"2000"//
                  });
                }
              } else {
              //
                this.listLoading=false;
                this.$message({
                    message: response.data.msg,
                    type: "error"
                  });
                
              }
              
            },
            response => {
              //
                this.listLoading=false;
                this.$message({
                    dangerouslyUseHTMLString: true,//html
                    message: '<svg class="icon" aria-hidden="true"> <use xlink:href="#icon-shengqi"></use> </svg> '+
                    response.body.message,
                    type: "error"
                  });
              console.log(response);
            }
          );
      },
  },

    //
    filters: {
          formatDate(time) {
              var date = new Date(time);
              return formatDate(date, 'yyyy-MM-dd hh:mm');
          }
    },
     
};
</script>

 <style lang='less'>

//  less
@import '../users/index.css';




















</style>



