<template>
<!-- truefalse -->
  <div v-if="userName==this.$store.state.nameler?false:true">
    <div class="topBanner">
      
      <el-tooltip class="item" effect="light" content="" placement="bottom">
      <p class='tog' @click="togglemenu">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-zhankai1"></use>
        </svg></p>
      </el-tooltip>

      <p class='left'> >> </p>

      <p class='right' @click="goBack">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-shouye"></use>
        </svg></p>

      <p class='right'>
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-character"></use>
        </svg>{{userName}}</p>

      <p class='right' @click="screenfull">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-quanping4"></use>
      </svg></p>

      <p class='right' title="">
      <router-link to="/yonghuguanli/mimaxiugai" class="roulink">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-mimazhongzhi"></use>
      </svg>
      </router-link>
      </p>

      <p class='right' @click="lastpage">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-fanhui2"></use>
      </svg></p>
    </div>

<div class="content">
        <div class="searcharea">
          <!-- el-formmodelel-form-itemel-form-itemprop -->
          <el-form ref="searchform" :model="searchform" label-width="100%" size="mini">
            <el-row :gutter="30">
              <el-col :span="6">
                  <el-form-item label="" prop="channelCode">
                    <el-select v-model="searchform.channelCode" placeholder="" clearable >
                      <!--  -->
                    <el-option v-for="item in channellist"
                              :label="item.channelCode"
                              :key="item.channelCode"
                              :value="item.channelCode"
                          ></el-option>
                  </el-select>
                  </el-form-item>
              </el-col>
              
              <el-col :span="6">
                <el-form-item label="" prop="startDate">
                    <el-date-picker class="shi" clearable
                      v-model="searchform.startDate"
                      value-format="yyyyMMdd"
                      type="date"
                      placeholder="">
                    </el-date-picker>
                </el-form-item>
              </el-col>
              <el-col :span="3">
                 <el-form-item label="" prop="endDate">
                    <el-date-picker class="shi" clearable
                      v-model="searchform.endDate"
                      value-format="yyyyMMdd"
                      type="date"
                      placeholder="">
                    </el-date-picker>
                </el-form-item>
              </el-col>
            </el-row>

              <el-row :gutter="30">
              <el-col :span="12">
                <div class="di">
                  <div class="wrapper">
                    <el-button type="primary" plain @click="search()" size="mini">
                      </el-button>
                      <el-button type="primary" plain @click="resetForm('searchform')" size="mini">
                      
                    </el-button>
                    <el-button type="primary" plain @click="getdialog()" size="mini">
                      
                    </el-button>
                  </div>
                </div>
              </el-col>

              <el-col :span="12">
                <el-button size="small" 
                :type="jiaoyan? '':'success'" 
                :loading="loadbutton" :disabled="jiaoyan"
                @click="checkfile()"></el-button>

                <el-button size="small" 
                :type="jiaoyanqueren? '':'success'"  
                :loading="loadbutton" :disabled="jiaoyanqueren"
                @click="checkfinish()"></el-button>

                <el-button size="small" 
                :type="ruzhangqueren? '':'success'"  
                :loading="loadbutton" :disabled="ruzhangqueren"
                @click="excute()"></el-button>
                </el-col>
            </el-row>
          </el-form>
        </div>

         <!--  -->
       <el-dialog title="" :visible.sync="dialogFormVisible" width="30%" center>
        <el-form :model="searchform">
           <el-form-item label=""
            prop="channelCode">
                    <el-select v-model="searchform.channelCode" placeholder="" >
                      <!--  -->
                    <el-option v-for="item in channellist" :label="item.channelCode"
                      :key="item.channelCode" :value="item.channelCode"></el-option>
                    </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-date-picker v-model="searchform.fileDate" value-format="yyyyMMdd"
                type="date" placeholder="" style="width:140px!important;">
              </el-date-picker>
            </el-form-item>
          </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false"> </el-button>
          <el-button type="primary" @click="jiansuo()"> </el-button>
        </div>
      </el-dialog>
        <!-- ===============================  =================================== -->
       <!-- @select="handleSelectionChange" @current-change="handleCurrentChange"-->
          <el-table :data="tableData"  border size="medium" stripe ref="multipleTable"
          v-loading="listLoading" 
          element-loading-text=""
          element-loading-spinner="el-icon-loading"
          element-loading-background="rgba(0, 0, 0, 0.8)" 
          @row-click="showRow"  highlight-current-row 
          @current-change="handleCurrentChanges"
          style="width: 100%; height:100%;">
            <el-table-column width="35">

            <template slot-scope="scope">
              <el-radio v-model="radio" :label="scope.$index">&nbsp;</el-radio>
            </template>

            </el-table-column>
            <el-table-column prop="channelCode" label="" width="100px" align="center">
            </el-table-column>
            <el-table-column prop="fileType" label="" width="100px" align="center">
            </el-table-column>
            <el-table-column prop="fileName" label="" align="center">
            </el-table-column>
            <el-table-column prop="totalMoney" label="" align="center">
            </el-table-column>
            <el-table-column prop="totalCount" label="" align="center">
            </el-table-column>
            <el-table-column prop="fileDate" label="" align="center">
            </el-table-column>
            <el-table-column prop="fileId" label="ID" width="100px" v-if=idshow  align="center">
            </el-table-column>
            <el-table-column prop="fileStatus" label=""  align="center">
              <template slot-scope="scope">
                <span v-if="scope.row.fileStatus == 'Not'"></span>
                <span v-else-if="scope.row.fileStatus == 'In'"></span>
                <span v-else-if="scope.row.fileStatus == 'Exception'"></span>
                <span v-else-if="scope.row.fileStatus == 'Reject'"></span>
                <span v-else-if="scope.row.fileStatus == 'Pass'"></span>
                <span v-else-if="scope.row.fileStatus == 'Check'"></span>
                <span v-else-if="scope.row.fileStatus == 'Fail'"></span>
                <span v-else-if="scope.row.fileStatus == 'Success'"></span>
                <span v-else></span>
              </template>
            </el-table-column>
            <!-- <el-table-column prop="checktime" label="" align="center">
            </el-table-column> -->
          </el-table>

          <!--  -->
          <div class="block">
            <el-pagination background style="text-align:center" 
            @size-change="handleSizeChange" 
            @current-change="handleCurrentChange" 
            :current-page="this.searchform.pageIndex"
             :page-sizes="[20,50,100]"  
             :page-size="this.searchform.pageSize" 
             layout="total, sizes, prev, pager, next" 
             :total="count"><!---->
            </el-pagination>
          </div>
</div>
  </div>
</template>
<script>
// 
import {formatDate} from '../../common/date.js';
import screenfull from 'screenfull'


//vuex
import {mapState} from 'vuex'

export default {
  data() {
    return {
      radio: '',
      idshow:false,//ID
      isFullscreen: false,
      loadbutton:false,//
      jiaoyan:true,//
      jiaoyanqueren:true,//
      ruzhangqueren:true,//
      checkresult:"",//
      active: 0,//
      count:0,//
      pageIndex:1,//
      pageSize: 50,//
      userName: "",
      status: "none",
      listLoading: false,
      dialogFormVisible:false,//
      //
      channellist:[],

      //
      tableData: [],

      searchform: {
        channelCode:"",//
        startDate:"",//
        endDate:"",//
        fileDate:"",//
        fileId:"",//id
        pageIndex:1,//
        pageSize: 50,//
      },
    };
  },
  // mounted
  mounted() {
    this.getName();//
    this.getchannellist();//
     window.onresize = () => {
            // ESC
            if (!this.checkFull()) {
              // esc
              this.isFullscreen = false
            }
          };
  },

  //html
created() {
},

  methods: {
    //
    lastpage(){
      window.history.go(-1);
    },
    //
    showRow(row) {
      if(row!=""){
        
      //radio
      this.radio = this.tableData.indexOf(row); 
      //id  
      this.getCurrentRow(this.tableData[this.tableData.indexOf(row)].fileStatus,this.tableData[this.tableData.indexOf(row)].fileId,this.tableData[this.tableData.indexOf(row)].channelCode);
      
      }
  },
    handleCurrentChanges(currentRow, oldCurrentRow){
          this.currentRow = currentRow;
           //id  
      this.getCurrentRow(this.tableData[this.tableData.indexOf(currentRow)].fileStatus,this.tableData[this.tableData.indexOf(currentRow)].fileId,this.tableData[this.tableData.indexOf(currentRow)].channelCode);
      
      },
    //
    getdialog(){
      this.dialogFormVisible=true
    },

    getCurrentRow(val,valid,valcode) {
      // console.log(val)//
      // console.log(valid)
      // console.log(valcode)
      if(val!=null){
        // 
        ////
        this.searchform.fileId=valid;
        this.searchform.channelCode=valcode;
        if(val=="Not"){
            this.jiaoyan=false;
            this.jiaoyanqueren=true;
            this.ruzhangqueren=true;
          }
          else if(val=="In" || val=="Success" || val=="Exception"  || val=="Reject" ){
            this.jiaoyan=true;
            this.jiaoyanqueren=true;
            this.ruzhangqueren=true;
          }
          else if(val=="Check" || val=="Fail"){
            this.jiaoyan=true;
            this.jiaoyanqueren=true;
            this.ruzhangqueren=false;
          }
          else if(val=="Pass"){
            this.jiaoyan=true;
            this.jiaoyanqueren=false;
            this.ruzhangqueren=true;
          }
       this.searchform.fileDate="";//
      }else if(val==null){
        //
        this.searchform.fileId = "";
        this.jiaoyan=true;//
        this.jiaoyanqueren=true;//
        this.ruzhangqueren=true;//
      }
   },

    //
    checkfile(){
       this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$http
          .post(
          this.$store.state.domain +"/earlySettleFile/check", this.searchform)
          //then()then()then()
          .then(  
          // Lambda
          response => {
               if(response.data.code == 0){
                 this.$confirm(response.data.msg, '', {
                  confirmButtonText: '',
                  showCancelButton:false
                }).then(() => {
                    this.jiaoyan=true;
                    this.jiaoyanqueren=true;
                    this.ruzhangqueren=true;
                  }).catch(() => {
                    //          
                  });
               }  
          },
          response => {
              //
              this.listLoading=false;
              this.$message({
                  dangerouslyUseHTMLString: true,//html
                  message: '<svg class="icon" aria-hidden="true"> <use xlink:href="#icon-shengqi"></use> </svg> '+
                  response.body.message,
                  type: "error"
                  });
              console.log(response);
          }
          );
        }).catch(() => {
            this.$message({
            type: 'info',
            message: ''
          });  
        });
      
    },

    //
    checkfinish(){
      this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
        this.$http
              .post(
              this.$store.state.domain +"/earlySettleFile/checkConfirm", this.searchform)
              //then()then()then()
              .then(  
              // Lambda
              response => {
                  if(response.data.code == 0){
                    this.$confirm(response.data.msg, '', {
                      confirmButtonText: '',
                      showCancelButton:false
                    }).then(() => {
                      this.jiaoyan=true;
                      this.jiaoyanqueren=true;
                      this.ruzhangqueren=true;
                      }).catch(() => {
                        //          
                      });
                  }  
              },
              response => {
                  //
                  this.listLoading=false;
                  this.$message({
                      dangerouslyUseHTMLString: true,//html
                      message: '<svg class="icon" aria-hidden="true"> <use xlink:href="#icon-shengqi"></use> </svg> '+
                      response.body.message,
                      type: "error"
                      });
                  console.log(response);
              }
              );
          }).catch(() => {
            this.$message({
            type: 'info',
            message: ''
          });  
        });
    },

    //
    excute(){
      this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
            this.$http
                .post(
                this.$store.state.domain +"/earlySettleFile/confirm", this.searchform)
                //then()then()then()
                .then(  
                // Lambda
                response => {
                    if(response.data.code == 0){
                      this.$confirm(response.data.msg, '', {
                        confirmButtonText: '',
                        showCancelButton:false
                      }).then(() => {
                        this.jiaoyan=true;
                        this.jiaoyanqueren=true;
                        this.ruzhangqueren=true;
                        }).catch(() => {
                          //          
                        });
                    }  
                },
                response => {
                    //
                    this.listLoading=false;
                    this.$message({
                        dangerouslyUseHTMLString: true,//html
                        message: '<svg class="icon" aria-hidden="true"> <use xlink:href="#icon-shengqi"></use> </svg> '+
                        response.body.message,
                        type: "error"
                        });
                    console.log(response);
                }
                );
           }).catch(() => {
            this.$message({
            type: 'info',
            message: ''
          });  
        });
    },
    // pagesize
      handleSizeChange(psize) {
        // 
        this.searchform.pageSize = psize;
        // 
        this.searchform.pageIndex =1;
        this.search();
      },

      // currentPage
      handleCurrentChange(pindex) {
        this.searchform.pageIndex = pindex;
        this.search();
      },
    // 
    search() {
     this.listLoading=true;
     this.searchform.fileId=""
     this.radio=""//
     //
     this.jiaoyan=true;
     this.jiaoyanqueren=true;
     this.ruzhangqueren=true;
      this.$http
          .post(
          this.$store.state.domain +"/earlySettleFile/query", this.searchform)
          //then()then()then()
          .then(  
          // Lambda
          response => {
               if(response.data.code == 0){
                 //
                this.tableData = response.data.result;
                this.pageSize= response.data.pageSize;
                this.pageIndex=response.data.pageIndex;
                this.count= response.data.total;
                this.listLoading=false; 

                if(this.tableData==null)
                {
                    this.$notify({
                    message: '',
                    type: 'warning',
                    duration:"2000"//
                    });
                }
               }  
          },
          response => {
              //
              this.listLoading=false;
              this.$message({
                  dangerouslyUseHTMLString: true,//html
                  message: '<svg class="icon" aria-hidden="true"> <use xlink:href="#icon-shengqi"></use> </svg> '+
                  response.body.message,
                  type: "error"
                  });
              console.log(response);
          }
          );

    },
    // 
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    // 
    jiansuo(){
      if(this.searchform.channelCode=="" && this.searchform.fileDate==""){
        this.$alert('', '', {
          confirmButtonText: '',
          callback: action => {
            //
          this.dialogFormVisible=true
          }
        });
      }
      else if(this.searchform.channelCode=="" || this.searchform.fileDate==""){
        this.$alert('', '', {
          confirmButtonText: '',
          callback: action => {
            //
          this.dialogFormVisible=true
          }
        });
      }
      else{
            this.dialogFormVisible=false;//
            //
            this.jiaoyan=true;
            this.jiaoyanqueren=true;
            this.ruzhangqueren=true;
            this.listLoading=true;
            this.$http
                .post(
                this.$store.state.domain +"/earlySettleFile/search", this.searchform)
                //then()then()then()
                .then(  
                // Lambda
                response => {
                    if (response.data.code ==0) {
                    //
                    this.tableData = response.data.result;
                    
                    this.pageSize= response.data.pageSize;
                    this.pageIndex=response.data.pageIndex;
                    this.count= response.data.total;
                    this.listLoading=false;

                    if(this.tableData==null)
                    {
                        this.$notify({
                        message: '',
                        type: 'warning',
                        duration:"2000"//
                        });
                    }
                    } else {
                      this.listLoading=false;
                    //
                      this.$message({
                          message: response.data.msg,
                          type: "error"
                        });
                    }
                    
                },
                response => {
                    //
                    this.listLoading=false;
                    this.$message({
                        dangerouslyUseHTMLString: true,//html
                        message: '<svg class="icon" aria-hidden="true"> <use xlink:href="#icon-shengqi"></use> </svg> '+
                        response.body.message,
                        type: "error"
                        });
                    console.log(response);
                }
                );
      }

    },

    
// 
      getchannellist() {
      let data = {
        channelCd: this.searchform.channelCd
      };
      this.$http
        .post(this.$store.state.domain +"/loanManage/channel/query", data)
          .then(
          //
          response => {
            if (response.data.code == 0) { 
                this.channellist = response.data.detail.result;
            } 
            //
            else {
              this.$message({
                message: response.data.msg,
                type: "error"
              });
            }
          },
          //
          response => {
            // console.log(response);
          }
        );
      },
    //
    getdate() {
          var now = new Date(),
              y = now.getFullYear(),
              m = now.getMonth() + 1,
              d = now.getDate();
          return y + "-" + (m < 10 ? "0" + m : m) + "-" + (d < 10 ? "0" + d : d) + " " + now.toTimeString().substr(0, 8);
      },
    /**
     * 
     */
    screenfull() {
      if (!screenfull.enabled) {
        this.$message({
          message: 'Your browser does not work',
          type: 'warning'
        })
        return false
      }
      screenfull.toggle()
      this.isFullscreen = true
    },
    /**
     * ESC
     */
    checkFull() {
      var isFull = document.fullscreenEnabled || window.fullScreen || document.webkitIsFullScreen || document.msFullscreenEnabled
      // to fix : false || undefined == undefined
      if (isFull === undefined) {
          isFull = false
      }
      return isFull
    },

    //
    togglemenu(){
        this.$store.state.isLeftHiden=!this.$store.state.isLeftHiden;
        console.log(this.$store.state.isLeftHiden)
    },

    //vue  sessionStorage
    getName() {
    let userName = sessionStorage.getItem("name");
    this.userName = userName;
    },
    // 
    goBack() {
    var _this = this;
            this.$confirm('?', '', {
                //type: 'warning'
            }).then(() => {
        window.sessionStorage.removeItem('name');//sessionname
                this.$router.push('/login');
            }).catch(() => {

            });
    },
  },

    //
    filters: {
            formatDate(time) {
                var date = new Date(time);
                return formatDate(date, 'yyyy-MM-dd hh:mm');
            }
    },
    
};
</script>

 <style lang='less'>

//  less
@import '../users/index.css';









































































































































































































































































































































































































































































</style>



