<template>

<!-- truefalse -->
  <div v-if="userName==this.$store.state.nameler?false:true">
    

    <div class="topBanner">
      
      <el-tooltip class="item" effect="light" content="" placement="bottom">
      <p class='tog' @click="togglemenu">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-zhankai1"></use>
        </svg></p>
      </el-tooltip>

      <p class='left'> >> </p>

      <p class='right' @click="goBack" >
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-shouye"></use>
        </svg></p>


      
      <p class='right' title="">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-character"></use>
        </svg>{{userName}}</p>
      
      <p class='right' @click="screenfull">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-quanping4"></use>
      </svg></p>

      <p class='right' title="">
      <router-link to="/yonghuguanli/mimaxiugai" class="roulink">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-mimazhongzhi"></use>
      </svg>
      </router-link>
      </p>

      <p class='right' @click="lastpage">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-fanhui2"></use>
      </svg></p>
    </div>
  <div class="content">
        <el-alert
            title=""
            type="warning"
            show-icon
            center
            :closable="false">
        </el-alert>

    <div class="buttonshow">
        <el-button type="warning" plain icon="el-icon-warning" @click="stopduilie()"></el-button>
        <el-button type="primary" plain icon="el-icon-refresh" @click="restart()"></el-button>
    </div>
  </div>
  </div>
</template>
<script>
// 
import {formatDate} from '../../common/date.js';
import screenfull from 'screenfull'
//vuex
// import {mapState} from 'vuex'

export default {
  data() {
      
    return {
      isFullscreen: false,
      userName: "",
    };
  },
  // mounted,html
  mounted() {
    this.getName();//

    window.onresize = () => {
            // ESC
            if (!this.checkFull()) {
              // esc
              this.isFullscreen = false
            }
          };

    // this.getlist();//
  },

  methods: {
    //
    lastpage(){
      window.history.go(-1);
    },
    //
    stopduilie(){
      this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
      this.$http
            .post(this.$store.state.domain +"/mqManage/stopMessageListener")
              .then(
              //
              response => {
                if (response.data.code == 0) { 
                    this.$message({
                    dangerouslyUseHTMLString: true,//html
                    message: '<svg class="icon" aria-hidden="true"> <use xlink:href="#icon-jiqiren"></use> </svg> '+
                    response.body.detail.result,
                    type: "success"
                    });
                } 
                //
                else {
                  this.$message({
                    message: response.data.msg,
                    type: "error"
                  });
                }
              },
              //
              response => {
                console.log(response);
              }
            );
            }).catch(() => {
            this.$message({
            type: 'info',
            message: ''
          });  
        });
    },

    //
    restart(){
      this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
      this.$http
            .post(this.$store.state.domain +"/mqManage/restartMessageListener")
              .then(
              //
              response => {
                if (response.data.code == 0) { 
                    this.$message({
                    dangerouslyUseHTMLString: true,//html
                    message: '<svg class="icon" aria-hidden="true"> <use xlink:href="#icon-jiqiren"></use> </svg> '+
                    response.body.detail.result,
                    type: "success"
                    });
                } 
                //
                else {
                  this.$message({
                    message: response.data.msg,
                    type: "error"
                  });
                }
              },
              //
              response => {
                console.log(response);
              }
            );
            }).catch(() => {
            this.$message({
            type: 'info',
            message: ''
          });  
        });
    },

    /**
     * 
     */
    screenfull() {
      if (!screenfull.enabled) {
        this.$message({
          message: 'Your browser does not work',
          type: 'warning'
        })
        return false
      }
      screenfull.toggle()
      this.isFullscreen = true
    },
    /**
     * ESC
     */
    checkFull() {
      var isFull = document.fullscreenEnabled || window.fullScreen || document.webkitIsFullScreen || document.msFullscreenEnabled
      // to fix : false || undefined == undefined
      if (isFull === undefined) {
          isFull = false
      }
      return isFull
    },


    //
    togglemenu(){
        this.$store.state.isLeftHiden=!this.$store.state.isLeftHiden;
        console.log(this.$store.state.isLeftHiden)
    },
    
    // 
      goBack() {
        var _this = this;
				this.$confirm('?', '', {
					//type: 'warning'
				}).then(() => {
          window.sessionStorage.removeItem('name');//sessionname
					this.$router.push('/login');
				}).catch(() => {
			});
    },
    //vue  sessionStorage
      getName() {
        let userName = sessionStorage.getItem("name");
        this.userName = userName;
      },


  },
    //
    filters: {
          formatDate(time) {
              var date = new Date(time);
              return formatDate(date, 'yyyy-MM-dd hh:mm');
          }
    },
     
};
</script>

 <style lang='less'>

//  less
@import '../users/index.css';

.buttonshow {
  width: 100%;
  height: 80px;
  margin-top: 20px;
  padding: 20px;

  text-align: center;

  border-top: 1px dashed rgba(93, 91, 91, .61);
}





































































</style>



