<template>
  <div class="wrapper-inner" id="app">
    <div id="dtop" class="gotop" title="" @click="movetop">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-fanhuidingbu" />
      </svg>
    </div>

    <div class="side-container" :class="{'layout-menu' : this.$store.state.isLeftHiden}">
      <side-menu></side-menu>
    </div>
    <div class="main-container" :class="{'container-width':this.$store.state.isLeftHiden}">
      <!--  -->
      <router-view></router-view>
    </div>
  </div>
</template>
<script>
import sideMenu from "../../components/SideMenu.vue";

//vuex
// import {mapState} from 'vuex'

export default {
  data() {
    return {
      userName: ""
    };
  },
  mounted() {
    this.getName();
    //
    this.openCenter();
    window.addEventListener("scroll", this.returntop, true); // 
  },
  methods: {
    //
    returntop() {
      //
      var htop;
      if (document.documentElement && document.documentElement.scrollTop) {
        htop = document.documentElement.scrollTop;
      } else if (document.body) {
        htop = document.body.scrollTop;
      }
      var dtops = document.getElementById("dtop"); //
      console.log("dtops", dtops);
      console.log("htop", htop);
      // console.log(dtops.style.display);
      //
      if (htop > 350) {
        dtops.style.display = "block";
      } else {
        dtops.style.display = "none";
      }
    },
    movetop() {
      window.scrollTo(0, -1);
      document.body.scrollTop = 0;
    },

    openCenter() {
      if (this.userName != null) {
        this.$message({
          dangerouslyUseHTMLString: true, //html
          message:
            '<svg class="icon" aria-hidden="true"> <use xlink:href="#icon-jiqiren"></use> </svg> ' +
            "  " +
            this.userName +
            "  ",
          type: "success",
          center: true
        });
      } else {
        this.$message({
          dangerouslyUseHTMLString: true,
          message:
            '<svg class="icon" aria-hidden="true"> <use xlink:href="#icon-shengqi"></use> </svg> ' +
            "",
          type: "warning",
          center: true
        });
        this.$router.push("/login");
      }
    },

    //vue  sessionStorage
    getName() {
      let userName = sessionStorage.getItem("name");
      this.userName = userName;
    }
  },
  watch: {},
  components: {
    sideMenu
    // headBanner
  }
};
</script>

<style lang='less'>
@import url("//unpkg.com/element-ui@2.4.11/lib/theme-chalk/index.css");
</style>



